import { useState, useEffect } from "react";
import axios from 'axios';
import AppURL from "../components/AppUrl";
import { Bar, Line } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import { format, parseISO } from 'date-fns';
import { fr } from 'date-fns/locale';

Chart.register(...registerables);

interface StockItem {
    _id: string;
    name: string;
    quantity: number;
    quantiteConsomee: number;
    unite: string;
    createdAt: string;
    purchaseHistory?: {
        date: string;
        quantityAdded: number;
    }[];
    consumptionHistory?: {
        quantityUsed: number;
        date: string;
        idPlats: string;
        quantityAdded: number;
    }[];
    stats?: StatsData; // Ajout des statistiques
}


interface StatsData {
    consumption: {
        total: number;
        count: number;
        data: { date: string; quantityAdded: number }[];

    },
    purchase: {
        total: number;
        count: number;
        data: { date: string; quantityAdded: number }[];
    };
}

const GestionStock = () => {
    const [items, setItems] = useState<StockItem[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");
    const [showModal, setShowModal] = useState(false);
    const [selectedItem, setSelectedItem] = useState<StockItem | null>(null);
    const [showDeleteConfirmModal, setShowDeleteConfirmModal] = useState(false);
    const [showUpdateModal, setShowUpdateModal] = useState(false);
    const [showAddQuantityModal, setShowAddQuantityModal] = useState(false);
    const [quantityToAdd, setQuantityToAdd] = useState(1);
    const [addQuantityDate, setAddQuantityDate] = useState("");
    const [showStatsModal, setShowStatsModal] = useState(false);
    const [activeStatsTab, setActiveStatsTab] = useState<'consumption' | 'purchase'>('consumption');
    const [updateItemData, setUpdateItemData] = useState({
        id: '',
        name: '',
        quantity: 0,
        unite: '',
    });

    const [newItem, setNewItem] = useState({
        name: "",
        quantity: "",
        unite: '',
        createdAt: "",
    });
    const [dateRange, setDateRange] = useState({
        start: new Date(),
        end: new Date()
    });
    const [showDatePickerModal, setShowDatePickerModal] = useState(false);






    const handleDeleteItem = async () => {
        try {
            await axios.delete(`${AppURL}/api/stocks/${selectedItem?._id}`);
            setShowDeleteConfirmModal(false);
            setSelectedItem(null);
            fetchItems();
        } catch (error) {
            console.error("Erreur lors de la suppression:", error);
            setError("Erreur lors de la suppression de l'item");
        }
    };

    const handleUpdateItem = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            await axios.put(`${AppURL}/api/stocks/${updateItemData.id}`, {
                name: updateItemData.name,
                quantity: updateItemData.quantity,
                unite: updateItemData.unite,
            });
            setShowUpdateModal(false);
            setSelectedItem(null);
            fetchItems();
        } catch (error) {
            console.error("Erreur lors de la mise à jour:", error);
            setError("Erreur lors de la modification de l'item");
        }
    };

    const handleAddQuantity = async () => {
        if (!selectedItem || !addQuantityDate) return;

        try {
            await axios.post(`${AppURL}/api/stocks/${selectedItem._id}/add-quantity`, {
                quantityAdded: quantityToAdd,
                date: addQuantityDate
            });

            setShowAddQuantityModal(false);
            setQuantityToAdd(1);
            setAddQuantityDate("");
            fetchItems();
            fetchItemDetails(selectedItem._id);
        } catch (error) {
            console.error("Erreur lors de l'ajout de quantité:", error);
            setError("Erreur lors de l'ajout de quantité");
        }
    };
    const fetchItemDetails = async (itemId: string) => {
        try {
            const response = await axios.get(`${AppURL}/api/stocks/${itemId}/details`);
            const item = response.data;

            // Générer les statistiques à partir des données existantes
            const stats = generateStatsFromItemData(item, dateRange.start, dateRange.end);

            setSelectedItem({
                ...item,
                stats
            });
        } catch (error) {
            console.error("Erreur lors du chargement des détails:", error);
        }
    };

    const generateStatsFromItemData = (item: StockItem, startDate: Date, endDate: Date): StatsData => {
        const adjustedStart = new Date(startDate);
        adjustedStart.setHours(0, 0, 0, 0);

        const adjustedEnd = new Date(endDate);
        adjustedEnd.setHours(23, 59, 59, 999);

        return {
            consumption: generateConsumptionStats(item, adjustedStart, adjustedEnd),
            purchase: generatePurchaseStats(item.purchaseHistory || [], adjustedStart, adjustedEnd)
        };
    };

    const generateConsumptionStats = (item: StockItem, startDate: Date, endDate: Date) => {
        if (!item.consumptionHistory || item.consumptionHistory.length === 0) {
            return { total: 0, count: 0, data: [] };
        }

        // Filtrer les consommations dans la période
        const filtered = item.consumptionHistory.filter(c => {
            const consumptionDate = new Date(c.date);
            return consumptionDate >= startDate && consumptionDate <= endDate;
        });

        // Grouper par jour
        const groupedByDay = filtered.reduce((acc, current) => {
            const dateStr = new Date(current.date).toISOString().split('T')[0]; // Format YYYY-MM-DD

            if (!acc[dateStr]) {
                acc[dateStr] = 0;
            }
            acc[dateStr] += current.quantityUsed;

            return acc;
        }, {} as Record<string, number>);

        // Convertir en tableau et trier par date
        const dailyData = Object.entries(groupedByDay)
            .map(([date, total]) => {
                // Créer une date en UTC
                const utcDate = new Date(date);
                utcDate.setMinutes(utcDate.getMinutes() + utcDate.getTimezoneOffset());
                return {
                    date: utcDate.toISOString(), // Stocker en UTC
                    quantityAdded: total
                };
            })
            .sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());

        return {
            total: filtered.reduce((sum, c) => sum + c.quantityUsed, 0),
            count: Object.keys(groupedByDay).length,
            data: dailyData
        };
    };




    const generatePurchaseStats = (history: { date: string; quantityAdded: number }[], startDate: Date, endDate: Date) => {
        const adjustedStart = new Date(startDate);
        adjustedStart.setUTCHours(0, 0, 0, 0);

        const adjustedEnd = new Date(endDate);
        adjustedEnd.setUTCHours(23, 59, 59, 999);

        const filtered = history.filter(p => {
            const purchaseDate = new Date(p.date);
            return purchaseDate >= adjustedStart && purchaseDate <= adjustedEnd;
        });

        return {
            total: filtered.reduce((sum, p) => sum + p.quantityAdded, 0),
            count: filtered.length,
            data: filtered
        };
    };

    useEffect(() => {
        fetchItems();
    }, []);

    const fetchItems = async () => {
        try {
            const response = await axios.get(`${AppURL}/api/stocks`);
            setItems(response.data);
            setLoading(false);
        } catch (error) {
            console.error("Erreur lors de la récupération des items:", error);
            setError("Erreur lors du chargement du stock");
            setLoading(false);
        }
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        setNewItem(prev => ({
            ...prev,
            [name]: name === "quantity" ? parseFloat(value) || 0 : value // Utilise parseFloat au lieu de parseInt
        }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            await axios.post(`${AppURL}/api/stocks`, newItem);
            setShowModal(false);
            setNewItem({
                name: "",
                quantity: "",
                unite: '',
                createdAt: "",
            });
            fetchItems();
        } catch (error) {
            console.error("Erreur lors de la création:", error);
            setError("Erreur lors de la création de l'item");
        }
    };

    if (loading) {
        return (
            <div className="flex justify-center items-center h-screen">
                <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                <span className="ml-3 text-lg">Chargement du stock...</span>
            </div>
        );
    }

    if (error) {
        return (
            <div className="max-w-4xl mx-auto p-4">
                <div className="bg-red-100 border-l-4 border-red-500 text-red-700 p-4 mb-4 rounded" role="alert">
                    <p className="font-medium">{error}</p>
                    <button
                        onClick={fetchItems}
                        className="mt-2 px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 transition"
                    >
                        Réessayer
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="mx-auto px-4 py-8 mt-15">
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-bold text-gray-800">Gestion du Stock</h1>
                <button
                    onClick={() => setShowModal(true)}
                    className="bg-green-950 hover:bg-green-700 text-white px-4 py-2 rounded-md flex items-center transition"
                >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clipRule="evenodd" />
                    </svg>
                    Ajouter un item
                </button>
            </div>

            {/* Liste des items */}
            <div className="bg-white rounded-lg shadow overflow-hidden">
                <div className="overflow-x-auto">
                    <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-50">
                            <tr>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Nom</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Quantité Totale</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Quantité Restante</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Unité</th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date création</th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {items.length > 0 ? (
                                items.map((item) => {
                                    const remainingQuantity = item.quantity - (item.quantiteConsomee || 0);
                                    return (
                                        <tr
                                            key={item._id}
                                            className="hover:bg-gray-50 cursor-pointer"
                                            onClick={() => {
                                                setSelectedItem(item);
                                                fetchItemDetails(item._id);
                                            }}
                                        >
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div className="text-sm font-medium text-gray-900">{item.name}</div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div className="text-sm text-gray-900">{item.quantity}</div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div className={`text-sm font-medium ${remainingQuantity > item.quantity * 0.3 ? 'text-green-600' :
                                                    remainingQuantity > item.quantity * 0.1 ? 'text-yellow-600' : 'text-red-600'
                                                    }`}>
                                                    {remainingQuantity}
                                                </div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div className="text-sm text-gray-900 capitalize">{item.unite}</div>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <div className="text-sm text-gray-900">
                                                    {new Date(item.createdAt).toLocaleDateString('fr-FR', { timeZone: 'UTC' })}
                                                </div>
                                            </td>
                                        </tr>
                                    );
                                })
                            ) : (
                                <tr>
                                    <td colSpan={5} className="px-6 py-4 text-center text-sm text-gray-500">
                                        Aucun item en stock
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
            </div>

            {/* Modal des détails de l'article */}
            {selectedItem && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-4xl max-h-[90vh] overflow-y-auto">
                        <div className="flex justify-between items-center mb-6">
                            <h2 className="text-2xl font-bold text-gray-800">
                                Détails de {selectedItem.name}
                            </h2>
                            <button
                                onClick={() => setSelectedItem(null)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                            <div className="bg-gray-50 p-4 rounded-lg">
                                <h3 className="text-lg font-semibold text-gray-700 mb-4 border-b pb-2">Informations de base</h3>
                                <div className="space-y-3">
                                    <div>
                                        <p className="text-sm text-gray-500">Nom</p>
                                        <p className="font-medium">{selectedItem.name}</p>
                                    </div>
                                    {/* Dans la section "Informations de base" */}
                                    <div>
                                        <p className="text-sm text-gray-500">Quantité totale</p>
                                        <p className="font-medium">{selectedItem.quantity} {selectedItem.unite}</p>
                                    </div>
                                    <div>
                                        <p className="text-sm text-gray-500">Quantité consommée</p>
                                        <p className="font-medium">{selectedItem.quantiteConsomee || 0} {selectedItem.unite}</p>
                                    </div>
                                    <div>
                                        <p className="text-sm text-gray-500">Quantité restante</p>
                                        <p className="font-medium text-blue-600">
                                            {selectedItem.quantity - (selectedItem.quantiteConsomee || 0)} {selectedItem.unite}
                                        </p>
                                    </div>
                                    <div>
                                        <p className="text-sm text-gray-500">Unité</p>
                                        <p className="font-medium capitalize">{selectedItem.unite}</p>
                                    </div>
                                    <div>
                                        <p className="text-sm text-gray-500">Date de création</p>
                                        <p className="font-medium">
                                            {new Date(selectedItem.createdAt).toLocaleDateString('fr-FR', { timeZone: 'UTC' })}
                                        </p>
                                    </div>
                                </div>
                            </div>


                            <div className="bg-gray-50 p-4 rounded-lg">
                                <h3 className="text-lg font-semibold text-gray-700 mb-4 border-b pb-2">Historique des achats</h3>
                                {selectedItem.purchaseHistory && selectedItem.purchaseHistory.length > 0 ? (
                                    <div className="overflow-y-auto max-h-60">
                                        <table className="min-w-full divide-y divide-gray-200">
                                            <thead>
                                                <tr>
                                                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Date</th>
                                                    <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Quantité</th>
                                                </tr>
                                            </thead>
                                            <tbody className="divide-y divide-gray-200">
                                                {selectedItem.purchaseHistory.map((purchase, index) => (
                                                    <tr key={index}>
                                                        <td className="px-4 py-2 whitespace-nowrap text-sm">
                                                            {new Date(purchase.date).toLocaleDateString('fr-FR', { timeZone: 'UTC' })}
                                                        </td>
                                                        <td className="px-4 py-2 whitespace-nowrap text-sm font-medium text-green-600">
                                                            +{purchase.quantityAdded} {selectedItem.unite}
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                ) : (
                                    <p className="text-sm text-gray-500">Aucun achat enregistré pour cet item</p>
                                )}
                            </div>
                        </div>

                        <div className="flex flex-wrap justify-end gap-3">
                            <button
                                onClick={() => setShowDatePickerModal(true)}
                                className="px-4 py-2 bg-purple-600 text-white rounded-md hover:bg-purple-700 flex items-center transition"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                                    <path d="M2 11a1 1 0 011-1h2a1 1 0 011 1v5a1 1 0 01-1 1H3a1 1 0 01-1-1v-5zM8 7a1 1 0 011-1h2a1 1 0 011 1v9a1 1 0 01-1 1H9a1 1 0 01-1-1V7zM14 4a1 1 0 011-1h2a1 1 0 011 1v12a1 1 0 01-1 1h-2a1 1 0 01-1-1V4z" />
                                </svg>
                                Voir les statistiques
                            </button>
                            <button
                                onClick={() => {
                                    setShowAddQuantityModal(true);
                                    setAddQuantityDate(new Date().toISOString().split('T')[0]);
                                }}
                                className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-green-700 flex items-center transition"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                                    <path fillRule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clipRule="evenodd" />
                                </svg>
                                Acheter à nouveau
                            </button>


                            <button
                                onClick={() => {
                                    setShowUpdateModal(true);
                                    setUpdateItemData({
                                        id: selectedItem._id,
                                        name: selectedItem.name,
                                        quantity: selectedItem.quantity,
                                        unite: selectedItem.unite,
                                    });
                                }}
                                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 flex items-center transition"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                                    <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                                </svg>
                                Modifier
                            </button>

                            <button
                                onClick={() => setShowDeleteConfirmModal(true)}
                                className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 flex items-center transition"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                                </svg>
                                Supprimer
                            </button>

                            <button
                                onClick={() => setSelectedItem(null)}
                                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                            >
                                Fermer
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal pour ajouter de la quantité */}
            {showAddQuantityModal && selectedItem && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md">
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-semibold text-gray-800">
                                Acheter {selectedItem.name} à nouveau
                            </h2>
                            <button
                                onClick={() => setShowAddQuantityModal(false)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="space-y-4">
                            <div>
                                <p className="text-sm text-gray-600 mb-1">
                                    Quantité actuelle: <span className="font-semibold">{selectedItem.quantity} {selectedItem.unite}</span>
                                </p>
                            </div>

                            <div>
                                <label htmlFor="quantityToAdd" className="block text-sm font-medium text-gray-700 mb-1">
                                    Quantité à ajouter *
                                </label>
                                <input
                                    type="number"
                                    id="quantityToAdd"
                                    value={quantityToAdd}
                                    onChange={(e) => {
                                        const value = Number(e.target.value);
                                        setQuantityToAdd(value);
                                      }}                                      
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="addQuantityDate" className="block text-sm font-medium text-gray-700 mb-1">
                                    Date d'achat *
                                </label>
                                <input
                                    type="date"
                                    id="addQuantityDate"
                                    value={addQuantityDate}
                                    onChange={(e) => setAddQuantityDate(e.target.value)}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>
                        </div>

                        <div className="flex justify-end space-x-3 mt-6">
                            <button
                                type="button"
                                onClick={() => {
                                    setShowAddQuantityModal(false);
                                    setQuantityToAdd(1);
                                }}
                                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                            >
                                Annuler
                            </button>
                            <button
                                type="button"
                                onClick={handleAddQuantity}
                                disabled={!addQuantityDate}
                                className={`px-4 py-2 rounded-md text-white flex items-center transition ${!addQuantityDate ? 'bg-gray-400 cursor-not-allowed' : 'bg-green-600 hover:bg-green-700'}`}
                            >
                                Confirmer l'achat
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal de confirmation de suppression */}
            {showDeleteConfirmModal && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md">
                        <div className="text-center">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto text-red-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                            </svg>
                            <h3 className="text-lg font-semibold text-gray-800 mt-4">Confirmer la suppression</h3>
                            <p className="mt-2 text-gray-600">Êtes-vous sûr de vouloir supprimer définitivement cet item ?</p>
                        </div>

                        <div className="flex justify-center space-x-4 mt-6">
                            <button
                                onClick={() => setShowDeleteConfirmModal(false)}
                                className="px-6 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                            >
                                Annuler
                            </button>
                            <button
                                onClick={handleDeleteItem}
                                className="px-6 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 transition"
                            >
                                Supprimer
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal de modification */}
            {showUpdateModal && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md">
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-semibold text-gray-800">Modifier l'item</h2>
                            <button
                                onClick={() => setShowUpdateModal(false)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <form onSubmit={handleUpdateItem} className="space-y-4">
                            <div>
                                <label htmlFor="updateName" className="block text-sm font-medium text-gray-700 mb-1">Nom *</label>
                                <input
                                    type="text"
                                    id="updateName"
                                    name="name"
                                    value={updateItemData.name}
                                    onChange={(e) => setUpdateItemData({ ...updateItemData, name: e.target.value })}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="updateQuantity" className="block text-sm font-medium text-gray-700 mb-1">Quantité *</label>
                                <input
                                    type="number"
                                    id="updateQuantity"
                                    name="quantity"
                                    min="0"
                                    value={updateItemData.quantity}
                                    onChange={(e) => setUpdateItemData({ ...updateItemData, quantity: parseFloat(e.target.value) || 0 })}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="updateunite" className="block text-sm font-medium text-gray-700 mb-1">Unité *</label>
                                <input
                                    type="text"
                                    id="updateunite"
                                    name="unité"
                                    value={updateItemData.unite}
                                    onChange={(e) => setUpdateItemData({ ...updateItemData, unite: e.target.value })}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div className="flex justify-end space-x-3 pt-4">
                                <button
                                    type="button"
                                    onClick={() => setShowUpdateModal(false)}
                                    className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                                >
                                    Annuler
                                </button>
                                <button
                                    type="submit"
                                    className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-green-700 transition"
                                >
                                    Enregistrer
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}

            {/* Modal de création */}
            {showModal && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md">
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-semibold text-gray-800">Ajouter un nouvel item</h2>
                            <button
                                onClick={() => setShowModal(false)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div>
                                <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">Nom *</label>
                                <input
                                    type="text"
                                    id="name"
                                    name="name"
                                    value={newItem.name}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="quantity" className="block text-sm font-medium text-gray-700 mb-1">Quantité *</label>
                                <input
                                    type="number"
                                    id="quantity"
                                    name="quantity"
                                    value={newItem.quantity}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="unite" className="block text-sm font-medium text-gray-700 mb-1">Unité *</label>
                                <input
                                    type="text"
                                    id="unite"
                                    name="unite"
                                    value={newItem.unite}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="createdAt" className="block text-sm font-medium text-gray-700 mb-1">Date de création *</label>
                                <input
                                    type="date"
                                    id="createdAt"
                                    name="createdAt"
                                    value={newItem.createdAt}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div className="flex justify-end space-x-3 pt-4">
                                <button
                                    type="button"
                                    onClick={() => setShowModal(false)}
                                    className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                                >
                                    Annuler
                                </button>
                                <button
                                    type="submit"
                                    className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-green-700 transition"
                                >
                                    Enregistrer
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}

            {/* Modal des statistiques */}
            {showStatsModal && selectedItem && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-2 sm:p-4">
                    <div className="bg-white rounded-lg shadow-xl w-full max-w-6xl mx-2 h-[90vh] max-h-screen flex flex-col">
                        {/* Header */}
                        <div className="px-6 py-4 border-b border-gray-200 flex justify-between items-center sticky top-0 bg-white z-10">
                            <h2 className="text-xl sm:text-2xl font-bold text-gray-800">
                                Statistiques - {selectedItem.name}
                            </h2>
                            <button
                                onClick={() => setShowStatsModal(false)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100 transition-colors"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        {/* Contenu principal avec défilement */}
                        <div className="flex-1 overflow-y-auto p-4 sm:p-6">
                            {/* Onglets */}
                            <div className="border-b border-gray-200 mb-6">
                                <nav className="flex space-x-4 sm:space-x-8">
                                    <button
                                        onClick={() => setActiveStatsTab('consumption')}
                                        className={`px-1 py-3 sm:py-4 border-b-2 font-medium text-xs sm:text-sm ${activeStatsTab === 'consumption'
                                            ? 'border-blue-500 text-blue-600'
                                            : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                                            }`}
                                    >
                                        Consommation
                                    </button>
                                    <button
                                        onClick={() => setActiveStatsTab('purchase')}
                                        className={`px-1 py-3 sm:py-4 border-b-2 font-medium text-xs sm:text-sm ${activeStatsTab === 'purchase'
                                            ? 'border-blue-500 text-blue-600'
                                            : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                                            }`}
                                    >
                                        Achats
                                    </button>
                                </nav>
                            </div>

                            {/* Contenu des onglets */}
                            {activeStatsTab === 'consumption' ? (
                                <div className="space-y-6">
                                    {/* Cartes de synthèse */}
                                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4">
                                        <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                            <h4 className="text-xs sm:text-sm font-medium text-gray-500 mb-1">Total consommé</h4>
                                            <p className="text-lg sm:text-xl font-semibold">
                                                {selectedItem.stats?.consumption.total || 0} {selectedItem.unite}
                                            </p>
                                        </div>
                                        <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                            <h4 className="text-xs sm:text-sm font-medium text-gray-500 mb-1">Période (jours)</h4>
                                            <p className="text-lg sm:text-xl font-semibold">
                                                {selectedItem.stats?.consumption.count || 0}
                                            </p>
                                        </div>
                                    </div>

                                    {/* Graphique de consommation */}
                                    <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                        <h4 className="text-sm sm:text-md font-medium mb-3">Historique de consommation</h4>
                                        <div className="h-60 sm:h-80">
                                            {selectedItem.stats?.consumption.data?.length ? (
                                                <Line
                                                    data={{
                                                        labels: selectedItem.stats.consumption.data.map(d =>
                                                            format(parseISO(d.date), 'dd/MM/yyyy', { locale: fr })
                                                        ),
                                                        datasets: [{
                                                            label: `Consommation (${selectedItem.unite})`,
                                                            data: selectedItem.stats.consumption.data.map(d => d.quantityAdded),
                                                            backgroundColor: 'rgba(5, 46, 22, 0.1)', // vert olive très foncé avec 10% opacité
                                                            borderColor: 'rgba(5, 46, 22, 0.8)',     // bordure plus visible
                                                            borderWidth: 2,
                                                            tension: 0.1,
                                                            fill: true
                                                        }]

                                                    }}
                                                    options={{
                                                        responsive: true,
                                                        maintainAspectRatio: false,
                                                        plugins: {
                                                            legend: {
                                                                position: 'top' as const,
                                                                labels: {
                                                                    boxWidth: 12,
                                                                    padding: 20,
                                                                    font: {
                                                                        size: 14
                                                                    }
                                                                }
                                                            },
                                                            tooltip: {
                                                                backgroundColor: 'rgba(0, 0, 0, 0.8)',
                                                                titleFont: { size: 14 },
                                                                bodyFont: { size: 12 },
                                                                padding: 10,
                                                                cornerRadius: 4
                                                            }
                                                        },
                                                        scales: {
                                                            y: {
                                                                beginAtZero: true,

                                                                title: {
                                                                    display: true,
                                                                    text: `Quantité (${selectedItem.unite})`,
                                                                    font: {
                                                                        size: 12
                                                                    }
                                                                },
                                                                ticks: {
                                                                    stepSize: 1,
                                                                    font: {
                                                                        size: 11
                                                                    }
                                                                }
                                                            },
                                                            x: {
                                                                grid: {
                                                                    display: false
                                                                },
                                                                ticks: {
                                                                    font: {
                                                                        size: 11
                                                                    }
                                                                }
                                                            }
                                                        }
                                                    }}
                                                />
                                            ) : (
                                                <div className="h-full flex items-center justify-center">
                                                    <p className="text-gray-500 text-sm">Pas de données disponibles</p>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                <div className="space-y-6">
                                    {/* Cartes de synthèse */}
                                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4">
                                        <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                            <h4 className="text-xs sm:text-sm font-medium text-gray-500 mb-1">Total acheté</h4>
                                            <p className="text-lg sm:text-xl font-semibold">
                                                {selectedItem.stats?.purchase.total || 0} {selectedItem.unite}
                                            </p>
                                        </div>
                                        <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                            <h4 className="text-xs sm:text-sm font-medium text-gray-500 mb-1">Nombre d'achats</h4>
                                            <p className="text-lg sm:text-xl font-semibold">
                                                {selectedItem.stats?.purchase.count || 0}
                                            </p>
                                        </div>
                                    </div>

                                    {/* Graphique des achats */}
                                    <div className="bg-white p-3 sm:p-4 rounded-lg shadow-sm border border-gray-100">
                                        <h4 className="text-sm sm:text-md font-medium mb-3">Historique des achats</h4>
                                        <div className="h-60 sm:h-80">
                                            {selectedItem.stats?.purchase.data?.length ? (
                                                <Bar
                                                    data={{
                                                        labels: selectedItem.stats.purchase.data.map(p => {
                                                            const date = new Date(p.date);
                                                            const correctedDate = new Date(date.getTime() + date.getTimezoneOffset() * 60000);
                                                            return format(correctedDate, 'dd/MM/yyyy', { locale: fr });
                                                        }),
                                                        datasets: [{
                                                            label: `Quantité achetée (${selectedItem.unite})`,
                                                            data: selectedItem.stats.purchase.data.map(p => p.quantityAdded),
                                                            backgroundColor: 'rgba(5, 46, 22, 0.1)', // vert olive très foncé avec 10% opacité
                                                            borderColor: 'rgba(5, 46, 22, 0.8)',     // bordure plus visible
                                                            borderWidth: 1,
                                                            borderRadius: 4
                                                        }]
                                                    }}
                                                    options={{
                                                        responsive: true,
                                                        maintainAspectRatio: false,
                                                        plugins: {
                                                            legend: {
                                                                position: 'top' as const,
                                                                labels: {
                                                                    boxWidth: 12,
                                                                    padding: 20,
                                                                    font: {
                                                                        size: 14
                                                                    }
                                                                }
                                                            },
                                                            tooltip: {
                                                                backgroundColor: 'rgba(0, 0, 0, 0.8)',
                                                                titleFont: { size: 14 },
                                                                bodyFont: { size: 12 },
                                                                padding: 10,
                                                                cornerRadius: 4
                                                            }
                                                        },
                                                        scales: {
                                                            y: {
                                                                beginAtZero: true,
                                                                grid: {
                                                                    display: false
                                                                },
                                                                title: {
                                                                    display: true,
                                                                    text: `Quantité (${selectedItem.unite})`,
                                                                    font: {
                                                                        size: 12
                                                                    }
                                                                },
                                                                ticks: {
                                                                    stepSize: 1,
                                                                    font: {
                                                                        size: 11
                                                                    }
                                                                }
                                                            },
                                                            x: {
                                                                grid: {
                                                                    display: false
                                                                },
                                                                ticks: {
                                                                    maxRotation: 45,
                                                                    minRotation: 45,
                                                                    font: {
                                                                        size: 11
                                                                    }
                                                                }
                                                            }
                                                        }
                                                    }}
                                                />
                                            ) : (
                                                <div className="h-full flex items-center justify-center">
                                                    <p className="text-gray-500 text-sm">Pas de données disponibles</p>
                                                </div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>

                        {/* Footer */}
                        <div className="px-6 py-4 border-t border-gray-200 flex justify-end sticky bottom-0 bg-white">
                            <button
                                onClick={() => setShowStatsModal(false)}
                                className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-green-700 transition-colors text-sm sm:text-base"
                            >
                                Fermer
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal de sélection de dates */}
            {showDatePickerModal && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md">
                        <div className="flex justify-between items-center mb-4">
                            <h2 className="text-xl font-semibold text-gray-800">
                                Sélectionnez la période
                            </h2>
                            <button
                                onClick={() => setShowDatePickerModal(false)}
                                className="text-gray-500 hover:text-gray-700 p-1 rounded-full hover:bg-gray-100"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="space-y-4">
                            <div>
                                <label htmlFor="startDate" className="block text-sm font-medium text-gray-700 mb-1">
                                    Date de début *
                                </label>
                                <input
                                    type="date"
                                    id="startDate"
                                    value={dateRange.start.toISOString().split('T')[0]}
                                    onChange={(e) => {
                                        const date = new Date(e.target.value);
                                        date.setMinutes(date.getMinutes() + date.getTimezoneOffset());
                                        setDateRange({ ...dateRange, start: date });
                                    }}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>

                            <div>
                                <label htmlFor="endDate" className="block text-sm font-medium text-gray-700 mb-1">
                                    Date de fin *
                                </label>
                                <input
                                    type="date"
                                    id="endDate"
                                    value={dateRange.end.toISOString().split('T')[0]}
                                    onChange={(e) => {
                                        const date = new Date(e.target.value);
                                        date.setMinutes(date.getMinutes() + date.getTimezoneOffset());
                                        setDateRange({ ...dateRange, end: date });
                                    }}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                    required
                                />
                            </div>
                        </div>

                        <div className="flex justify-end space-x-3 mt-6">
                            <button
                                type="button"
                                onClick={() => setShowDatePickerModal(false)}
                                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 transition"
                            >
                                Annuler
                            </button>
                            <button
                                type="button"
                                onClick={() => {
                                    setShowDatePickerModal(false);
                                    setShowStatsModal(true);
                                    // Recalculer les stats avec la nouvelle période
                                    if (selectedItem) {
                                        const stats = generateStatsFromItemData(selectedItem, dateRange.start, dateRange.end);
                                        setSelectedItem(prev => prev ? { ...prev, stats } : null);
                                    }
                                }}
                                className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-green-700 transition"
                            >
                                Valider
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default GestionStock;