import { Component, ChangeEvent } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';

interface PlatIngredient {
    stockId: string;
    quantity: string;
    _id?: string;
}

interface Plat {
    _id: string;
    name: string;
    ingredients: PlatIngredient[];
    __v?: number;
}

interface StockItem {
    _id: string;
    name: string;
    unite: string;
}

interface State {
    plats: Plat[];
    stocks: StockItem[];
    showModal: boolean;
    showDetailModal: boolean;
    currentPlat: Plat | null;
    newPlatName: string;
    ingredients: PlatIngredient[];
    editMode: boolean;
    loading: boolean;
}

class Plats extends Component<{}, State> {
    state: State = {
        plats: [],
        stocks: [],
        showModal: false,
        showDetailModal: false,
        currentPlat: null,
        newPlatName: '',
        ingredients: [],
        editMode: false,
        loading: true
    };

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async () => {
        this.setState({ loading: true });
        try {
            const [platsRes, stocksRes] = await Promise.all([
                axios.get<Plat[]>(`${AppURL}/api/plats/getAll`),
                axios.get<StockItem[]>(`${AppURL}/api/stocks`)
            ]);
            this.setState({
                plats: platsRes.data,
                stocks: stocksRes.data,
                loading: false
            });
        } catch (error) {
            console.error("Error fetching data:", error);
            this.setState({ loading: false });
        }
    };

    toggleModal = () => {
        this.setState(prevState => ({
            showModal: !prevState.showModal,
            newPlatName: '',
            ingredients: [],
            editMode: false
        }));
    };

    toggleDetailModal = (plat: Plat | null = null) => {
        this.setState(prevState => ({
            showDetailModal: !prevState.showDetailModal,
            currentPlat: plat,
            editMode: false,
            newPlatName: plat?.name || '',
            ingredients: plat ? [...plat.ingredients] : []
        }));
    };

    handleAddIngredient = () => {
        this.setState(prevState => ({
            ingredients: [...prevState.ingredients, { stockId: '', quantity: '' }]
        }));
    };

    handleRemoveIngredient = (index: number) => {
        const updatedIngredients = [...this.state.ingredients];
        updatedIngredients.splice(index, 1);
        this.setState({ ingredients: updatedIngredients });
    };

    handleIngredientChange = (index: number, field: keyof PlatIngredient, value: string) => {
        const updatedIngredients = [...this.state.ingredients];
        updatedIngredients[index][field] = value;
        this.setState({ ingredients: updatedIngredients });
    };

    handleCreatePlat = async () => {
        try {
            const { newPlatName, ingredients, editMode, currentPlat } = this.state;
            
            if (editMode && currentPlat) {
                await axios.put(`${AppURL}/api/plats/${currentPlat._id}`, {
                    name: newPlatName,
                    ingredients
                });
                // Ferme la modale et recharge les données
                this.setState({ showDetailModal: false });
            } else {
                await axios.post(`${AppURL}/api/plats/create`, {
                    name: newPlatName,
                    ingredients
                });
                this.toggleModal();
            }
            
            this.fetchData();
        } catch (error) {
            console.error("Error saving plat:", error);
        }
    };

    handleDeletePlat = async (id: string) => {
        try {
            if (window.confirm("Êtes-vous sûr de vouloir supprimer ce plat?")) {
                await axios.delete(`${AppURL}/api/plats/${id}`);
                this.fetchData();
                this.toggleDetailModal();
            }
        } catch (error) {
            console.error("Error deleting plat:", error);
        }
    };

    enterEditMode = () => {
        this.setState({ editMode: true });
    };

    getStockName = (stockId: string) => {
        const stock = this.state.stocks.find(s => s._id === stockId);
        return stock ? stock.name : 'Inconnu';
    };

    getStockUnit = (stockId: string) => {
        const stock = this.state.stocks.find(s => s._id === stockId);
        return stock ? stock.unite : '';
    };

    render() {
        const { plats, stocks, showModal, showDetailModal, currentPlat, 
                newPlatName, ingredients, editMode, loading } = this.state;

        if (loading) {
            return (
                <div className="flex justify-center items-center h-screen">
                    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                </div>
            );
        }

        return (
            <div className="container mx-auto px-4 py-8 max-w-full mt-10">
                <div className="bg-white rounded-xl shadow-md p-6  mx-auto">
                    <div className="flex justify-between items-center mb-8">
                        <h2 className="text-3xl font-bold text-gray-800 flex items-center">
                            <span className="mr-3">🍽️</span> Mes Plats
                        </h2>
                        <button
                            onClick={this.toggleModal}
                            className="bg-gradient-to-r from-green-950 to-green-950 hover:from-green-600 hover:to-green-700 text-white font-medium py-2 px-6 rounded-lg flex items-center transition-all shadow-lg hover:shadow-xl"
                        >
                            <span className="mr-2">+</span> Ajouter un plat
                        </button>
                    </div>
                    
                    <div className="mb-8">
                        {plats.length === 0 ? (
                            <div className="text-center py-12">
                                <p className="text-gray-500 text-lg">Aucun plat disponible</p>
                                <button
                                    onClick={this.toggleModal}
                                    className="mt-4 bg-green-950 hover:bg-green-600 text-white font-medium py-2 px-4 rounded-lg"
                                >
                                    Créer votre premier plat
                                </button>
                            </div>
                        ) : (
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                                {plats.map(plat => (
                                    <div 
                                        key={plat._id}
                                        className="border border-gray-200 rounded-lg p-4 hover:border-green-300 hover:shadow-md transition-all cursor-pointer"
                                        onClick={() => this.toggleDetailModal(plat)}
                                    >
                                        <h3 className="font-bold text-lg text-gray-800 mb-2">{plat.name}</h3>
                                        <div className="text-sm text-gray-600">
                                            <p>{plat.ingredients.length} ingrédient{plat.ingredients.length > 1 ? 's' : ''}</p>
                                            <ul className="mt-2 space-y-1">
                                                {plat.ingredients.slice(0, 2).map((ing, idx) => (
                                                    <li key={idx} className="truncate">
                                                        • {this.getStockName(ing.stockId)}: {ing.quantity} {this.getStockUnit(ing.stockId)}
                                                    </li>
                                                ))}
                                                {plat.ingredients.length > 2 && (
                                                    <li className="text-gray-400">+ {plat.ingredients.length - 2} autres...</li>
                                                )}
                                            </ul>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* Modal pour créer/mettre à jour un plat */}
                {showModal && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-xl shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                            <div className="p-8">
                                <h3 className="text-2xl font-bold text-gray-800 mb-6">
                                    {editMode ? 'Modifier le plat' : 'Créer un nouveau plat'}
                                </h3>

                                <div className="mb-6">
                                    <label className="block text-gray-700 mb-3 font-medium text-lg">Nom du plat</label>
                                    <input
                                        type="text"
                                        placeholder="Nom du plat"
                                        value={newPlatName}
                                        onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                            this.setState({ newPlatName: e.target.value })
                                        }
                                        className="w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent text-lg"
                                    />
                                </div>

                                <div className="mb-6">
                                    <div className="flex justify-between items-center mb-3">
                                        <label className="block text-gray-700 font-medium text-lg">Ingrédients</label>
                                        <button
                                            onClick={this.handleAddIngredient}
                                            className="text-sm bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded-lg flex items-center"
                                        >
                                            <span className="mr-1">+</span> Ajouter
                                        </button>
                                    </div>
                                    <div className="space-y-4">
                                        {ingredients.map((ingredient, index) => {
                                            return (
                                                <div key={index} className="flex items-center space-x-3">
                                                    <select
                                                        value={ingredient.stockId}
                                                        onChange={(e: ChangeEvent<HTMLSelectElement>) =>
                                                            this.handleIngredientChange(index, 'stockId', e.target.value)
                                                        }
                                                        className="flex-1 p-3 border border-gray-300 rounded-lg text-lg"
                                                    >
                                                        <option value="">Sélectionner un ingrédient</option>
                                                        {stocks.map(stock => (
                                                            <option key={stock._id} value={stock._id}>
                                                                {stock.name}
                                                            </option>
                                                        ))}
                                                    </select>

                                                    <input
                                                        type="number"
                                                        placeholder="Qté"
                                                        value={ingredient.quantity}
                                                        onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                                            this.handleIngredientChange(index, 'quantity', e.target.value)
                                                        }
                                                        className="w-24 p-3 border border-gray-300 rounded-lg text-lg"
                                                        min="0"
                                                        step="0.01"
                                                    />

                                                    <span className="w-20 text-gray-500 text-lg">
                                                        {ingredient.stockId ? this.getStockUnit(ingredient.stockId) : ''}
                                                    </span>

                                                    <button
                                                        onClick={() => this.handleRemoveIngredient(index)}
                                                        className="text-red-500 hover:text-red-700 p-2 text-xl"
                                                    >
                                                        ×
                                                    </button>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>

                                <div className="flex justify-end space-x-4 pt-6">
                                    <button
                                        onClick={this.toggleModal}
                                        className="px-6 py-3 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors text-lg"
                                    >
                                        Annuler
                                    </button>
                                    <button
                                        onClick={this.handleCreatePlat}
                                        className="px-6 py-3 bg-green-950 hover:bg-green-700 text-white rounded-lg flex items-center transition-colors text-lg"
                                        disabled={!newPlatName || ingredients.length === 0}
                                    >
                                        <span className="mr-2">💾</span> Enregistrer
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Modal pour voir les détails d'un plat */}
                {showDetailModal && currentPlat && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-xl shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                            <div className="p-8">
                                {editMode ? (
                                    <h3 className="text-2xl font-bold text-gray-800 mb-6">Modifier le plat</h3>
                                ) : (
                                    <div className="flex justify-between items-center mb-6">
                                        <h3 className="text-2xl font-bold text-gray-800">Détails du plat</h3>
                                        <div className="flex space-x-3">
                                            <button
                                                onClick={this.enterEditMode}
                                                className="text-green-950 hover:text-green-700 p-2"
                                                title="Modifier"
                                            >
                                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" viewBox="0 0 20 20" fill="currentColor">
                                                    <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                                                </svg>
                                            </button>
                                            <button
                                                onClick={() => this.handleDeletePlat(currentPlat._id)}
                                                className="text-red-500 hover:text-red-700 p-2"
                                                title="Supprimer"
                                            >
                                                <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" viewBox="0 0 20 20" fill="currentColor">
                                                    <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                                                </svg>
                                            </button>
                                        </div>
                                    </div>
                                )}

                                {editMode ? (
                                    <>
                                        <div className="mb-6">
                                            <label className="block text-gray-700 mb-3 font-medium text-lg">Nom du plat</label>
                                            <input
                                                type="text"
                                                value={newPlatName}
                                                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                                    this.setState({ newPlatName: e.target.value })
                                                }
                                                className="w-full p-4 border border-gray-300 rounded-lg text-lg"
                                            />
                                        </div>

                                        <div className="mb-6">
                                            <div className="flex justify-between items-center mb-3">
                                                <label className="block text-gray-700 font-medium text-lg">Ingrédients</label>
                                                <button
                                                    onClick={this.handleAddIngredient}
                                                    className="text-sm bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded-lg flex items-center"
                                                >
                                                    <span className="mr-1">+</span> Ajouter
                                                </button>
                                            </div>
                                            <div className="space-y-4">
                                                {ingredients.map((ingredient, index) => {
                                                    return (
                                                        <div key={index} className="flex items-center space-x-3">
                                                            <select
                                                                value={ingredient.stockId}
                                                                onChange={(e: ChangeEvent<HTMLSelectElement>) =>
                                                                    this.handleIngredientChange(index, 'stockId', e.target.value)
                                                                }
                                                                className="flex-1 p-3 border border-gray-300 rounded-lg text-lg"
                                                            >
                                                                <option value="">Sélectionner un ingrédient</option>
                                                                {stocks.map(stock => (
                                                                    <option key={stock._id} value={stock._id}>
                                                                        {stock.name}
                                                                    </option>
                                                                ))}
                                                            </select>

                                                            <input
                                                                type="number"
                                                                value={ingredient.quantity}
                                                                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                                                    this.handleIngredientChange(index, 'quantity', e.target.value)
                                                                }
                                                                className="w-24 p-3 border border-gray-300 rounded-lg text-lg"
                                                                min="0"
                                                                step="0.01"
                                                            />

                                                            <span className="w-20 text-gray-500 text-lg">
                                                                {ingredient.stockId ? this.getStockUnit(ingredient.stockId) : ''}
                                                            </span>

                                                            <button
                                                                onClick={() => this.handleRemoveIngredient(index)}
                                                                className="text-red-500 hover:text-red-700 p-2 text-xl"
                                                            >
                                                                ×
                                                            </button>
                                                        </div>
                                                    );
                                                })}
                                            </div>
                                        </div>
                                    </>
                                ) : (
                                    <>
                                        <div className="mb-8">
                                            <h4 className="text-2xl font-semibold text-gray-800">{currentPlat.name}</h4>
                                        </div>

                                        <div className="mb-6">
                                            <h5 className="font-medium text-gray-700 mb-4 text-xl">Ingrédients:</h5>
                                            <ul className="space-y-4">
                                                {currentPlat.ingredients.map((ingredient, index) => (
                                                    <li key={index} className="flex justify-between items-center bg-gray-50 p-4 rounded-lg">
                                                        <span className="font-medium text-lg">{this.getStockName(ingredient.stockId)}</span>
                                                        <span className="text-gray-600 text-lg">
                                                            {ingredient.quantity} {this.getStockUnit(ingredient.stockId)}
                                                        </span>
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    </>
                                )}

                                <div className="flex justify-end space-x-4 pt-6">
                                    {editMode ? (
                                        <>
                                            <button
                                                onClick={() => this.setState({ editMode: false })}
                                                className="px-6 py-3 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 transition-colors text-lg"
                                            >
                                                Annuler
                                            </button>
                                            <button
                                                onClick={this.handleCreatePlat}
                                                className="px-6 py-3 bg-green-950 hover:bg-green-700 text-white rounded-lg transition-colors text-lg"
                                                disabled={!newPlatName || ingredients.length === 0}
                                            >
                                                Enregistrer
                                            </button>
                                        </>
                                    ) : (
                                        <button
                                            onClick={() => this.toggleDetailModal()}
                                            className="px-6 py-3 bg-gray-600 hover:bg-gray-700 text-white rounded-lg transition-colors text-lg"
                                        >
                                            Fermer
                                        </button>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        );
    }
}

export default Plats;