import { useEffect, useState } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';

interface Message {
    _id: string;
    fullName: string;
    email: string;
    phone: string;
    message: string;
    Seen: string[];
    createdAt: string;
    updatedAt: string;
}

interface UserData {
    name: string;
    email: string;
    id: string;
    role: string;
}

const Messages = () => {
    const [currentUser, setCurrentUser] = useState<UserData | null>(null);
    const [messages, setMessages] = useState<Message[]>([]);
    const [selectedMessage, setSelectedMessage] = useState<Message | null>(null);
    const [error, setError] = useState('');
    const userName = currentUser?.name || '';

    useEffect(() => {
        const userData = localStorage.getItem('userData');
        if (!userData) {
            throw new Error('Utilisateur non connecté');
        }

        const parsedUser = JSON.parse(userData) as UserData;
        setCurrentUser(parsedUser);
        fetchMessages();
    }, []);

    const handleMessageClick = async (message: Message) => {
        setSelectedMessage(message);

        if (!message.Seen.includes(userName)) {
            try {
                await axios.patch(`${AppURL}/api/ContactUs/mark-seen`, {
                    messageId: message._id,
                    userName: userName
                });

                setMessages((prev) =>
                    prev.map((m) =>
                        m._id === message._id
                            ? { ...m, Seen: [...m.Seen, userName] }
                            : m
                    )
                );
            } catch (error) {
                console.error('Erreur lors du marquage comme vu:', error);
            }
        }
    };

    const fetchMessages = async () => {
        try {
            const response = await axios.get(`${AppURL}/api/ContactUs/getAll`);
            setMessages(response.data);
        } catch (err) {
            console.error('Erreur:', err);
            setError("Erreur lors du chargement des messages.");
        }
    };

    return (
        <div className="p-4 sm:p-6">
            <h1 className="text-2xl font-bold mt-20 mb-6">Messages reçus</h1>

            {error && <p className="text-red-500 mb-4">{error}</p>}

            <div className="bg-white rounded shadow overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200 text-sm">
                    <thead className="bg-gray-50">
                        <tr>
                            <th className="px-4 py-2 text-left">Nom</th>
                            <th className="px-4 py-2 text-left">Email</th>
                            <th className="px-4 py-2 text-left">Date</th>
                            <th className="px-4 py-2 text-left">Statut</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {messages?.map((m) => { // Ajout du ?. pour le optional chaining
                            const isSeen = m?.Seen?.length > 0; // Protection supplémentaire
                            const bgColor = isSeen ? 'bg-gray-100' : 'bg-yellow-100';

                            return (
                                <tr
                                    key={m?._id}
                                    className={`${bgColor} hover:bg-opacity-70 cursor-pointer`}
                                    onClick={() => m && handleMessageClick(m)} // Protection contre m null/undefined
                                >
                                    <td className="px-4 py-2">{m?.fullName}</td>
                                    <td className="px-4 py-2">{m?.email}</td>
                                    <td className="px-4 py-2">{m?.createdAt ? new Date(m.createdAt).toLocaleDateString() : '-'}</td>
                                    <td className="px-4 py-2">
                                        {isSeen ? (
                                            <span className="text-gray-700">Lu</span>
                                        ) : (
                                            <span className="text-yellow-600 font-semibold">Nouveau</span>
                                        )}
                                    </td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </div>

            {selectedMessage && (
                <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-lg shadow-xl w-full max-w-md p-6 relative">
                        <button
                            className="absolute top-2 right-2 text-gray-600 hover:text-black text-xl"
                            onClick={() => setSelectedMessage(null)}
                        >
                            &times;
                        </button>

                        <h2 className="text-xl font-bold mb-4">Détails du message</h2>
                        <div className="space-y-4 text-sm">
                            <div>
                                <h3 className="font-semibold">De:</h3>
                                <p>{selectedMessage.fullName} ({selectedMessage.email})</p>
                                {selectedMessage.phone && <p>Téléphone: {selectedMessage.phone}</p>}
                            </div>


                            <div>
                                <h3 className="font-semibold">Message:</h3>
                                <p className="whitespace-pre-line">{selectedMessage.message}</p>
                            </div>

                            <div className="text-gray-500 text-xs">
                                <p>Reçu le: {new Date(selectedMessage.createdAt).toLocaleString()}</p>
                                {selectedMessage.Seen.length > 0 && (
                                    <p>Vu par: {selectedMessage.Seen.join(', ')}</p>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Messages;