import { Component, ChangeEvent } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';
import { format, parseISO, isSameDay } from 'date-fns';
import { fr } from 'date-fns/locale';

interface Supplement {
    stockId: string;
    quantite: number;
    _id?: string;
}

interface Vente {
    _id: string;
    idPlat: string;
    quantite: number;
    supplementaires: Supplement[];
    date: string;
}

interface Plat {
    _id: string;
    name: string;
}

interface StockItem {
    _id: string;
    name: string;
    unite: string;
}

interface State {
    ventes: Vente[];
    plats: Plat[];
    stocks: StockItem[];
    selectedDate: Date;
    showModal: boolean;
    currentVente: Vente | null;
    editMode: boolean;
    selectedPlat: string;
    quantite: number;
    supplements: Supplement[];
    addingSupplement: boolean;
    loading: boolean;
}

class Ventes extends Component<{}, State> {
    state: State = {
        ventes: [],
        plats: [],
        stocks: [],
        selectedDate: new Date(),
        showModal: false,
        currentVente: null,
        editMode: false,
        selectedPlat: '',
        quantite: 1,
        supplements: [],
        addingSupplement: false,
        loading: true
    };

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async () => {
        this.setState({ loading: true });
        try {
            const [ventesRes, platsRes, stocksRes] = await Promise.all([
                axios.get<Vente[]>(`${AppURL}/api/ventes`),
                axios.get<Plat[]>(`${AppURL}/api/plats/getAll`),
                axios.get<StockItem[]>(`${AppURL}/api/stocks`)
            ]);
            this.setState({
                ventes: ventesRes.data,
                plats: platsRes.data,
                stocks: stocksRes.data,
                loading: false
            });
        } catch (error) {
            console.error("Error fetching data:", error);
            this.setState({ loading: false });
        }
    };

    handleDateChange = (dateString: string) => {
        // Correction du problème de date (timezone)
        const date = new Date(dateString);
        const adjustedDate = new Date(date.getTime() + date.getTimezoneOffset() * 60000);
        this.setState({ selectedDate: adjustedDate });
    };

    getVentesForSelectedDate = () => {
        return this.state.ventes.filter(vente => 
            isSameDay(parseISO(vente.date), this.state.selectedDate
        ))
    };

    toggleModal = (vente: Vente | null = null, editMode: boolean = false) => {
        this.setState(prevState => ({
            showModal: !prevState.showModal,
            currentVente: vente,
            editMode,
            selectedPlat: vente?.idPlat || '',
            quantite: vente?.quantite || 1,
            supplements: vente ? [...vente.supplementaires] : [],
            addingSupplement: false
        }));
    };

    toggleAddSupplement = () => {
        this.setState(prevState => ({
            addingSupplement: !prevState.addingSupplement
        }));
    };

    handleAddSupplement = () => {
        this.setState(prevState => ({
            supplements: [...prevState.supplements, { stockId: '', quantite: 0.1 }]
        }));
    };

    handleRemoveSupplement = (index: number) => {
        const updatedSupplements = [...this.state.supplements];
        updatedSupplements.splice(index, 1);
        this.setState({ supplements: updatedSupplements });
    };

    handleSupplementChange = (index: number, field: keyof Supplement, value: string | number) => {
        const updatedSupplements = [...this.state.supplements];
        updatedSupplements[index][field] = value as never;
        this.setState({ supplements: updatedSupplements });
    };

    handleCreateVente = async () => {
        try {
            const { selectedPlat, quantite, supplements, selectedDate, editMode, currentVente } = this.state;
            
            const venteData = {
                idPlat: selectedPlat,
                quantite,
                supplementaires: supplements,
                date: selectedDate.toISOString()
            };

            if (editMode && currentVente) {
                await axios.put(`${AppURL}/api/ventes/${currentVente._id}`, venteData);
            } else {
                await axios.post(`${AppURL}/api/ventes`, venteData);
            }
            
            this.toggleModal();
            this.fetchData();
        } catch (error) {
            console.error("Error saving vente:", error);
        }
    };

    handleDeleteVente = async (id: string) => {
        try {
            if (window.confirm("Êtes-vous sûr de vouloir supprimer cette vente?")) {
                await axios.delete(`${AppURL}/api/ventes/${id}`);
                this.fetchData();
            }
        } catch (error) {
            console.error("Error deleting vente:", error);
        }
    };

    getPlatName = (platId: string) => {
        const plat = this.state.plats.find(p => p._id === platId);
        return plat ? plat.name : 'Inconnu';
    };

    getStockName = (stockId: string) => {
        const stock = this.state.stocks.find(s => s._id === stockId);
        return stock ? stock.name : 'Inconnu';
    };

    getStockUnit = (stockId: string) => {
        const stock = this.state.stocks.find(s => s._id === stockId);
        return stock ? stock.unite : '';
    };

    render() {
        const { 
            plats, stocks, selectedDate, 
            showModal, editMode,
            selectedPlat, quantite, supplements, addingSupplement,
            loading 
        } = this.state;

        const ventesForDate = this.getVentesForSelectedDate();

        if (loading) {
            return (
                <div className="flex justify-center items-center h-screen">
                    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                </div>
            );
        }

        return (
            <div className=" mx-auto px-2 sm:px-4 py-4 mt-10">
                <div className="bg-white rounded-xl shadow-md p-4 sm:p-6">
                    <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
                        <h2 className="text-2xl sm:text-3xl font-bold text-gray-800">📊 Ventes</h2>
                        <div className="flex flex-col sm:flex-row items-end sm:items-center gap-3 w-full sm:w-auto">
                            <input
                                type="date"
                                value={format(selectedDate, 'yyyy-MM-dd')}
                                onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                    this.handleDateChange(e.target.value)
                                }
                                className="p-2 border border-gray-300 rounded-lg w-full sm:w-auto"
                            />
                            <button
                                onClick={() => this.toggleModal()}
                                className="bg-green-950 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg w-full sm:w-auto"
                            >
                                + Ajouter
                            </button>
                        </div>
                    </div>

                    <div className="mb-6">
                        <h3 className="text-lg sm:text-xl font-semibold mb-4">
                            Ventes du {format(selectedDate, 'PPPP', { locale: fr })}
                        </h3>
                        
                        {ventesForDate.length === 0 ? (
                            <p className="text-gray-500 text-center py-8">Aucune vente pour cette date</p>
                        ) : (
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                                {ventesForDate.map(vente => (
                                    <div 
                                        key={vente._id}
                                        className="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 cursor-pointer transition-colors"
                                        onClick={() => this.toggleModal(vente, true)}
                                    >
                                        <div className="flex justify-between items-start">
                                            <div>
                                                <h4 className="font-bold text-lg">
                                                    {this.getPlatName(vente.idPlat)} × {vente.quantite}
                                                </h4>
                                                {vente.supplementaires.length > 0 && (
                                                    <div className="mt-2">
                                                        <p className="text-sm font-medium text-gray-600">Suppléments:</p>
                                                        <ul className="text-sm text-gray-500">
                                                            {vente.supplementaires.map((sup, idx) => (
                                                                <li key={idx}>
                                                                    • {this.getStockName(sup.stockId)}: {sup.quantite} {this.getStockUnit(sup.stockId)}
                                                                </li>
                                                            ))}
                                                        </ul>
                                                    </div>
                                                )}
                                            </div>
                                            <button
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    this.handleDeleteVente(vente._id);
                                                }}
                                                className="text-red-500 hover:text-red-700 p-1"
                                                title="Supprimer"
                                            >
                                                🗑️
                                            </button>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* Modal pour ajouter/modifier une vente */}
                {showModal && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-30 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-xl shadow-xl w-full max-w-md max-h-[90vh] overflow-y-auto">
                            <div className="p-6">
                                <h3 className="text-xl font-bold text-gray-800 mb-4">
                                    {editMode ? 'Modifier la vente' : 'Ajouter une vente'}
                                </h3>

                                <div className="mb-4">
                                    <label className="block text-gray-700 mb-2">Plat</label>
                                    <select
                                        value={selectedPlat}
                                        onChange={(e: ChangeEvent<HTMLSelectElement>) => 
                                            this.setState({ selectedPlat: e.target.value })
                                        }
                                        className="w-full p-2 border border-gray-300 rounded-lg"
                                    >
                                        <option value="">Sélectionner un plat</option>
                                        {plats.map(plat => (
                                            <option key={plat._id} value={plat._id}>
                                                {plat.name}
                                            </option>
                                        ))}
                                    </select>
                                </div>

                                <div className="mb-4">
                                    <label className="block text-gray-700 mb-2">Quantité</label>
                                    <input
                                        type="number"
                                        min="1"
                                        step="1"
                                        value={quantite}
                                        onChange={(e: ChangeEvent<HTMLInputElement>) => 
                                            this.setState({ quantite: parseFloat(e.target.value) || 1 })
                                        }
                                        className="w-full p-2 border border-gray-300 rounded-lg"
                                    />
                                </div>

                                <div className="mb-4">
                                    <div className="flex justify-between items-center mb-2">
                                        <label className="block text-gray-700">Supplémentaires</label>
                                        <button
                                            onClick={this.toggleAddSupplement}
                                            className="text-sm bg-blue-500 hover:bg-blue-600 text-white py-1 px-3 rounded-lg"
                                        >
                                            {addingSupplement ? 'Masquer' : 'Ajouter'}
                                        </button>
                                    </div>

                                    {addingSupplement && (
                                        <div className="mb-4 p-3 bg-gray-50 rounded-lg">
                                            <button
                                                onClick={this.handleAddSupplement}
                                                className="mb-3 text-sm bg-green-950 hover:bg-green-600 text-white py-1 px-3 rounded-lg"
                                            >
                                                + Nouveau supplément
                                            </button>
                                            <div className="space-y-3">
                                                {supplements.map((supplement, index) => (
                                                    <div key={index} className="flex items-center gap-2">
                                                        <select
                                                            value={supplement.stockId}
                                                            onChange={(e: ChangeEvent<HTMLSelectElement>) =>
                                                                this.handleSupplementChange(index, 'stockId', e.target.value)
                                                            }
                                                            className="flex-1 p-2 border border-gray-300 rounded-lg text-sm"
                                                        >
                                                            <option value="">Sélectionner un item</option>
                                                            {stocks.map(stock => (
                                                                <option key={stock._id} value={stock._id}>
                                                                    {stock.name}
                                                                </option>
                                                            ))}
                                                        </select>

                                                        <input
                                                            type="number"
                                                            min="0.1"
                                                            step="0.1"
                                                            value={supplement.quantite}
                                                            onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                                                this.handleSupplementChange(index, 'quantite', parseFloat(e.target.value) || 0)
                                                            }
                                                            className="w-20 p-2 border border-gray-300 rounded-lg text-sm"
                                                        />

                                                        <span className="text-xs text-gray-500 w-12 truncate">
                                                            {supplement.stockId ? this.getStockUnit(supplement.stockId) : ''}
                                                        </span>

                                                        <button
                                                            onClick={() => this.handleRemoveSupplement(index)}
                                                            className="text-red-500 hover:text-red-700 p-1"
                                                        >
                                                            ×
                                                        </button>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                    )}

                                    {supplements.length > 0 && (
                                        <div className="mt-2">
                                            <p className="text-sm font-medium mb-1">Suppléments ajoutés:</p>
                                            <ul className="text-sm text-gray-600 space-y-1">
                                                {supplements.map((sup, idx) => (
                                                    <li key={idx} className="flex justify-between items-center bg-gray-100 p-2 rounded">
                                                        <span>{this.getStockName(sup.stockId)}</span>
                                                        <span>{sup.quantite} {this.getStockUnit(sup.stockId)}</span>
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    )}
                                </div>

                                <div className="flex justify-end space-x-3">
                                    <button
                                        onClick={() => this.toggleModal()}
                                        className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
                                    >
                                        Annuler
                                    </button>
                                    <button
                                        onClick={this.handleCreateVente}
                                        className="px-4 py-2 bg-green-950 hover:bg-green-700 text-white rounded-lg"
                                        disabled={!selectedPlat || quantite < 1}
                                    >
                                        {editMode ? 'Mettre à jour' : 'Enregistrer'}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        );
    }
}

export default Ventes;