import { useState } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    try {
      const response = await axios.post(`${AppURL}/api/employees/login`, {
        email,
        password
      });

      if (response.data.message === "Connexion réussie") {
        localStorage.setItem('userData', JSON.stringify({
          name: response.data.user.name,
          email: response.data.user.email,
          role: response.data.user.role,
          id: response.data.user.id,
        }));

        window.location.href = '/feuille-de-temps';
      }
    } catch (err) {
      console.error('Erreur:', err);
      setError('Échec de la connexion. Veuillez vérifier vos identifiants.');
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-tr from-green-950 via-green-950 to-green-900 flex flex-col items-center justify-center p-4">
      
      {/* Titre principal */}
      <h1 className="text-4xl font-bold text-white mb-8 drop-shadow-md text-center">
        Chevrefeuille Lonicera
      </h1>

      <div className="bg-white rounded-2xl shadow-2xl w-full max-w-md px-8 py-10">
        <h2 className="text-2xl font-semibold text-center text-gray-800 mb-6">Connexion</h2>

        {error && (
          <div className="mb-6 px-4 py-3 bg-red-100 border border-red-300 text-red-700 rounded-md text-sm text-center">
            {error}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label htmlFor="email" className="block text-sm font-semibold text-gray-700 mb-1">
              Email
            </label>
            <input
              type="text"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value.toLowerCase())}
              placeholder="Votre email"
              className="w-full px-4 py-2 border rounded-lg shadow-sm focus:ring-2 focus:ring-green-600 focus:outline-none"
              required
            />
          </div>

          <div>
            <label htmlFor="password" className="block text-sm font-semibold text-gray-700 mb-1">
              Mot de passe
            </label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Votre mot de passe"
              className="w-full px-4 py-2 border rounded-lg shadow-sm focus:ring-2 focus:ring-green-600 focus:outline-none"
              required
            />
          </div>

          <button
            type="submit"
            className="w-full py-2 rounded-lg bg-green-950 text-white font-semibold hover:bg-green-700 transition duration-300 shadow-md"
          >
            Se connecter
          </button>
        </form>
      </div>
    </div>
  );
};

export default Login;
