import { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";

const NavbarAuth = () => {
  const location = useLocation();
  const [isOpen, setIsOpen] = useState(false);
  const [role, setRole] = useState<string | null>(null);

  useEffect(() => {
    const userData = localStorage.getItem("userData");
    if (userData) {
      const parsed = JSON.parse(userData);
      setRole(parsed.role);
    }
  }, []);

  // Liens disponibles selon le rôle
  const commonLinks = [
    { path: "/feuille-de-temps", label: "Feuille de temps" },
  ];

  const managerOnlyLinks = [
    { path: "/employee", label: "Employé" },
    { path: "/reservations", label: "Réservations" },
    { path: "/stock", label: "Stock" },
    { path: "/plats", label: "Plats" },
    { path: "/ventes", label: "ventes" },
    { path: "/factures", label: "Factures" },
    { path: "/Messages", label: "Messages" },
    
    
  ];

  const linksToDisplay = role === "manager" ? [...commonLinks, ...managerOnlyLinks] : commonLinks;

  return (
    <nav className="bg-gradient-to-r from-green-950 via-green-950 to-green-950 text-white p-4 fixed w-full top-0 shadow-lg z-50">
      <div className="container mx-auto flex justify-between items-center">
        {/* Titre non cliquable */}
        <div className="text-2xl font-extrabold text-white">Chèvrefeuille Management</div>

        {/* Bouton Burger */}
        <button
          className="text-white md:hidden focus:outline-none"
          onClick={() => setIsOpen(!isOpen)}
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d={isOpen ? "M6 18L18 6M6 6l12 12" : "M4 6h16M4 12h16M4 18h16"}
            />
          </svg>
        </button>

        {/* Menu de Navigation */}
        <ul
          className={`md:flex md:space-x-8 md:items-center absolute md:static top-16 right-0 bg-green-800 md:bg-transparent w-full md:w-auto ${isOpen ? "block" : "hidden"
            } space-y-4 md:space-y-0 p-4 md:p-0`}
        >
          {linksToDisplay.map((link) => (
            <li key={link.path}>
              <Link
                to={link.path}
                onClick={() => setIsOpen(false)}
                className={`block md:inline hover:text-green-300 transition duration-300 ${location.pathname === link.path
                    ? "text-white border-b-2 border-white"
                    : ""
                  }`}
              >
                {link.label}
              </Link>
            </li>
          ))}

          <li>
            <button
              onClick={() => {
                localStorage.removeItem("userData");
                window.location.href = "/";
              }}
              className="block md:inline bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded transition duration-300"
            >
              Déconnexion
            </button>
          </li>
        </ul>
      </div>
    </nav>
  );
};

export default NavbarAuth;
