import { Route, Routes, useLocation } from "react-router-dom";
import Login from "./pages/Login";
import Employee from "./pages/Employee";
import NavbarAuth from "./components/NavbarAuth";
import TimeCard from "./pages/TimeCard";
import GestionStock from "./pages/GestionStock";
import Reservation from "./pages/Reservation";
import Messages from "./pages/Messages";
import Plats from "./pages/Plats";
import Ventes from "./pages/Ventes";
import FactureManager from "./pages/FactureManager";

export default function App() {
  const location = useLocation();

  const showNavbar = location.pathname !== "/";

  return (
    <>
      {showNavbar && <NavbarAuth />}
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="/employee" element={<Employee />} />
        <Route path="/feuille-de-temps" element={<TimeCard />} />
        <Route path="/stock" element={<GestionStock />} />
        <Route path="/Reservations" element={<Reservation />} />
        <Route path="/Messages" element={<Messages />} />
        <Route path="/plats" element={<Plats />} />
        <Route path="/ventes" element={<Ventes />} />
        <Route path="/factures" element={<FactureManager />} />
      </Routes>
    </>
  );
}
