import { useEffect, useState } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';

interface Reservation {
    _id: string;
    fullName: string;
    phone: string;
    email: string;
    guests: number;
    date: string;
    time: string;
    Seen: string[];
    Confirmation?: boolean;
    ConfirmationSent: boolean;
    ConfirmedBy: string;
    ConfirmationDate: string;
    createdAt: string;
    updatedAt: string;
    ClientConfirmation?: boolean;
    preferredLanguage: string;
    contactMethod: string;
}

interface UserData {
    name: string;
    email: string;
    id: string;
    role: string;
}

const Reservation = () => {
    const [currentUser, setCurrentUser] = useState<UserData | null>(null);
    const [reservations, setReservations] = useState<Reservation[]>([]);
    const [filteredReservations, setFilteredReservations] = useState<Reservation[]>([]);
    const [selectedReservation, setSelectedReservation] = useState<Reservation | null>(null);
    const [error, setError] = useState('');
    const [dateRange, setDateRange] = useState({
        start: '',
        end: ''
    });
    const [loading, setLoading] = useState(false);
    const [activeFilter, setActiveFilter] = useState<'all' | 'called' | 'not_called'>('all');
    const userName = currentUser?.name || '';
    const [datesSelected, setDatesSelected] = useState(false);

    // Initialisation
    useEffect(() => {
        const userData = localStorage.getItem('userData');
        if (!userData) {
            throw new Error('Utilisateur non connecté');
        }

        const parsedUser = JSON.parse(userData) as UserData;
        setCurrentUser(parsedUser);
    }, []);

    // Fetch reservations with date range
    const fetchReservations = async () => {
        setLoading(true);
        try {
            const response = await axios.get(`${AppURL}/api/reservations/reservations`, {
                params: {
                    startDate: dateRange.start,
                    endDate: dateRange.end
                }
            });
            setReservations(response.data);
            setFilteredReservations(response.data);
            setLoading(false);
        } catch (err) {
            console.error('Erreur:', err);
            setError("Erreur lors du chargement des réservations.");
            setLoading(false);
        }
    };

    // Apply filter
    useEffect(() => {
        let filtered = [...reservations];

        switch (activeFilter) {
            case 'called':
                filtered = filtered.filter(r => r.ConfirmationSent === true);
                break;
            case 'not_called':
                filtered = filtered.filter(r => r.ConfirmationSent !== true);
                break;
            default:
                // 'all' - no filter
                break;
        }

        setFilteredReservations(filtered);
    }, [activeFilter, reservations]);

    // Group reservations by date
    const groupReservationsByDate = () => {
        const grouped: { [key: string]: Reservation[] } = {};

        filteredReservations.forEach(reservation => {
            const date = reservation.date;
            if (!grouped[date]) {
                grouped[date] = [];
            }
            grouped[date].push(reservation);
        });

        return grouped;
    };

    const handleConfirmReservation = async () => {
        if (!selectedReservation || !currentUser) return;

        const now = new Date().toLocaleString();

        try {
            await axios.patch(`${AppURL}/api/reservations/confirm`, {
                reservationId: selectedReservation._id,
                confirmedBy: currentUser.name,
                confirmationDate: now,
            });
            fetchReservations();
            setSelectedReservation(null);
        } catch (error) {
            console.error("Erreur de confirmation :", error);
        }
    };

    const handleRefuseReservation = async () => {
        if (!selectedReservation || !currentUser) return;

        const now = new Date().toLocaleString();

        try {
            await axios.patch(`${AppURL}/api/reservations/refuse`, {
                reservationId: selectedReservation._id,
                confirmedBy: currentUser.name,
                confirmationDate: now,
            });
            fetchReservations();
            setSelectedReservation(null);
        } catch (error) {
            console.error("Erreur de refus :", error);
        }
    };

    const handleReservationClick = async (reservation: Reservation) => {
        setSelectedReservation(reservation);

        if (!reservation.Seen.includes(userName)) {
            try {
                await axios.patch(`${AppURL}/api/reservations/mark-seen`, {
                    reservationId: reservation._id,
                    userName: userName
                });

                setReservations(prev =>
                    prev.map(r =>
                        r._id === reservation._id
                            ? { ...r, Seen: [...r.Seen, userName] }
                            : r
                    )
                );
            } catch (error) {
                console.error('Erreur lors du marquage comme vu:', error);
            }
        }
    };

    // Date selection form
    if (!datesSelected) {
        return (
            <div className="min-h-screen bg-gray-100 flex items-center justify-center p-4 sm:p-6">
                <div className="w-full max-w-lg bg-white shadow-xl rounded-2xl overflow-hidden">
                    <div className="bg-green-950 p-6 sm:p-8">
                        <h1 className="text-2xl sm:text-3xl font-bold text-white text-center">Sélection des dates</h1>
                    </div>
                    <div className="p-6 sm:p-8 space-y-6">
                        <div>
                            <label className="block text-sm font-semibold text-gray-700 mb-1">Date de début</label>
                            <input
                                type="date"
                                value={dateRange.start}
                                onChange={(e) => setDateRange({ ...dateRange, start: e.target.value })}
                                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-green-600 focus:border-transparent"
                                required
                            />
                        </div>

                        <div>
                            <label className="block text-sm font-semibold text-gray-700 mb-1">Date de fin</label>
                            <input
                                type="date"
                                value={dateRange.end}
                                onChange={(e) => setDateRange({ ...dateRange, end: e.target.value })}
                                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-green-600 focus:border-transparent"
                                required
                            />
                        </div>

                        <button
                            onClick={() => {
                                if (dateRange.start && dateRange.end) {
                                    setDatesSelected(true);
                                    fetchReservations();
                                }
                            }}
                            disabled={!dateRange.start || !dateRange.end || loading}
                            className={`w-full py-2 px-4 rounded-lg text-white text-center font-medium transition duration-200 ${!dateRange.start || !dateRange.end || loading
                                    ? 'bg-gray-300 cursor-not-allowed'
                                    : 'bg-green-950 hover:bg-green-700'
                                }`}
                        >
                            {loading ? 'Chargement...' : 'Valider'}
                        </button>

                        {error && (
                            <div className="p-3 bg-red-100 text-red-700 text-sm rounded-lg border border-red-300">
                                {error}
                            </div>
                        )}
                    </div>
                </div>
            </div>

        );
    }

    const groupedReservations = groupReservationsByDate();

    // Main reservation view
    return (
        <div className="p-4 sm:p-6 bg-white min-h-screen mt-15">
            <div className="flex justify-between items-center mb-6">
                <h1 className="text-2xl font-bold text-green-950">
                    Réservations du {dateRange.start} au {dateRange.end}
                </h1>
                <button
                    onClick={() => {
                        setDatesSelected(false);
                        setDateRange({ start: '', end: '' });
                        setReservations([]);
                    }}
                    className="px-4 py-2 bg-green-950 text-white rounded-md hover:bg-gray-300"
                >
                    Changer les dates
                </button>
            </div>

            {/* Filter Buttons */}
            <div className="mb-6 flex gap-2 p-2 bg-gray-100 rounded-lg">
                <button
                    onClick={() => setActiveFilter('all')}
                    className={`px-4 py-2 rounded-md flex-1 text-center ${activeFilter === 'all'
                        ? 'bg-white shadow text-indigo-600 font-medium'
                        : 'text-gray-600 hover:bg-gray-50'
                        }`}
                >
                    Toutes ({reservations.length})
                </button>
                <button
                    onClick={() => setActiveFilter('called')}
                    className={`px-4 py-2 rounded-md flex-1 text-center ${activeFilter === 'called'
                        ? 'bg-white shadow text-green-600 font-medium'
                        : 'text-gray-600 hover:bg-gray-50'
                        }`}
                >
                    Rappelées ({reservations.filter(r => r.ConfirmationSent === true).length})
                </button>
                <button
                    onClick={() => setActiveFilter('not_called')}
                    className={`px-4 py-2 rounded-md flex-1 text-center ${activeFilter === 'not_called'
                        ? 'bg-white shadow text-yellow-600 font-medium'
                        : 'text-gray-600 hover:bg-gray-50'
                        }`}
                >
                    Non rappelées ({reservations.filter(r => r.ConfirmationSent !== true).length})
                </button>
            </div>


            {/* Reservations List Grouped by Date */}
            <div className="bg-white rounded-xl shadow-md overflow-hidden">
                {Object.keys(groupedReservations).length > 0 ? (
                    Object.entries(groupedReservations).map(([date, reservationsForDate], index) => {
                        const totalGuests = reservationsForDate.reduce((sum, r) => sum + r.guests, 0);

                        return (
                            <div key={date} className={`${index > 0 ? 'mt-10' : ''}`}>
                                <div className="flex items-center justify-between px-6 py-4 bg-green-950 text-white rounded-t-xl shadow-inner">
                                    <h3 className="text-lg font-semibold">
                                        {date}
                                    </h3>
                                    <span className="text-white text-xl">
                                        {totalGuests} {totalGuests > 1 ? 'personnes' : 'personne'}
                                    </span>
                                </div>

                                <div className="divide-y divide-gray-100">
                                    {reservationsForDate.map((r) => {
                                        const status =
                                            r.ClientConfirmation === true ? 'Confirmé par client' :
                                                r.ClientConfirmation === false ? 'Refusé par client' :
                                                    r.Confirmation === true ? 'Rappelé' :
                                                        r.Confirmation === false ? 'Refusé' :
                                                            r.Seen.length > 0 ? 'Vu' : 'Nouveau';

                                        const statusColor =
                                            status === 'Confirmé par client' ? 'bg-green-100 text-green-800' :
                                                status === 'Refusé par client' ? 'bg-red-100 text-red-800' :
                                                    status === 'Rappelé' ? 'bg-blue-100 text-blue-800' :
                                                        status === 'Refusé' ? 'bg-orange-100 text-orange-800' :
                                                            status === 'Vu' ? 'bg-gray-100 text-gray-800' : 'bg-yellow-100 text-yellow-800';

                                        return (
                                            <div
                                                key={r._id}
                                                className="hover:bg-gray-100 transition duration-150 ease-in-out cursor-pointer flex flex-wrap sm:flex-nowrap items-center px-6 py-4"
                                                onClick={() => handleReservationClick(r)}
                                            >
                                                <div className="flex-1 min-w-0">
                                                    <p className="text-sm font-semibold text-gray-900">{r.fullName}</p>
                                                    <p className="text-xs text-gray-500 truncate">{r.email}</p>
                                                </div>
                                                <div className="w-24 text-sm text-gray-500 text-center">{r.time}</div>
                                                <div className="w-20 text-sm text-gray-500 text-center">{r.guests} pers.</div>
                                                <div className="ml-auto">
                                                    <span className={`px-3 py-1 inline-block text-xs font-medium rounded-full ${statusColor}`}>
                                                        {status}
                                                    </span>
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        );
                    })
                ) : (
                    <div className="p-8 text-center text-gray-500">
                        Aucune réservation trouvée pour cette période
                    </div>
                )}
            </div>


            {/* Reservation Details Modal */}
            {selectedReservation && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-lg shadow-xl w-full max-w-md max-h-[90vh] overflow-y-auto">
                        <div className="p-6">
                            <div className="flex justify-between items-start">
                                <h2 className="text-xl font-bold text-gray-800">Détails de la réservation</h2>
                                <button
                                    className="text-gray-500 hover:text-gray-700"
                                    onClick={() => setSelectedReservation(null)}
                                >
                                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path>
                                    </svg>
                                </button>
                            </div>

                            <div className="mt-4 space-y-4">
                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Nom complet</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.fullName}</p>
                                    </div>
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Email</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.email}</p>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Téléphone</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.phone || '-'}</p>
                                    </div>
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Nombre de personnes</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.guests}</p>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Date</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.date}</p>
                                    </div>
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Heure</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.time}</p>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Langue préférée</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.preferredLanguage}</p>
                                    </div>
                                    <div>
                                        <h3 className="text-sm font-medium text-gray-500">Méthode de contact</h3>
                                        <p className="mt-1 text-sm text-gray-900">{selectedReservation.contactMethod}</p>
                                    </div>
                                </div>

                                <div>
                                    <h3 className="text-sm font-medium text-gray-500">Statut</h3>
                                    <p className="mt-1 text-sm text-gray-900">
                                        {selectedReservation.Confirmation === true ? 'Rappelée' :
                                            selectedReservation.Confirmation === false ? 'Refusée' :
                                                selectedReservation.ConfirmationSent ? 'Traitée' : 'Non traitée'}
                                    </p>
                                </div>

                                {selectedReservation.ConfirmationSent && (
                                    <>
                                        {selectedReservation.ConfirmedBy && (
                                            <div>
                                                <h3 className="text-sm font-medium text-gray-500">
                                                    {selectedReservation.Confirmation ? 'Rappelée par' : 'Refusée par'}
                                                </h3>
                                                <p className="mt-1 text-sm text-gray-900">{selectedReservation.ConfirmedBy}</p>
                                            </div>
                                        )}

                                        {selectedReservation.ConfirmationDate && (
                                            <div>
                                                <h3 className="text-sm font-medium text-gray-500">
                                                    {selectedReservation.Confirmation ? 'Date de rappel' : 'Date de refus'}
                                                </h3>
                                                <p className="mt-1 text-sm text-gray-900">{selectedReservation.ConfirmationDate}</p>
                                            </div>
                                        )}
                                    </>
                                )}

                                <div>
                                    <h3 className="text-sm font-medium text-gray-500">Confirmation client</h3>
                                    <p className="mt-1 text-sm text-gray-900">
                                        {selectedReservation.ClientConfirmation === true ? 'Oui' :
                                            selectedReservation.ClientConfirmation === false ? 'Non' : 'En attente'}
                                    </p>
                                </div>

                                <div>
                                    <h3 className="text-sm font-medium text-gray-500">Vu par</h3>
                                    <p className="mt-1 text-sm text-gray-900">
                                        {selectedReservation.Seen.length > 0
                                            ? selectedReservation.Seen.join(', ')
                                            : 'Personne'}
                                    </p>
                                </div>

                                <div className="border-t border-gray-200 pt-4">
                                    <h3 className="text-sm font-medium text-gray-500">Date de création</h3>
                                    <p className="mt-1 text-sm text-gray-900">
                                        {new Date(selectedReservation.createdAt).toLocaleString()}
                                    </p>
                                </div>
                            </div>

                            {currentUser?.role === "manager" && !selectedReservation.ConfirmationSent && (
                                <div className="mt-6 flex justify-between">
                                    <button
                                        onClick={() => {
                                            if (window.confirm('Êtes-vous sûr de vouloir refuser cette réservation ?')) {
                                                handleRefuseReservation();
                                            }
                                        }}
                                        className="px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500"
                                    >
                                        Refuser
                                    </button>
                                    <button
                                        onClick={() => {
                                            if (window.confirm('Êtes-vous sûr de vouloir rappeler cette réservation ?')) {
                                                handleConfirmReservation();
                                            }
                                        }}
                                        className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500"
                                    >
                                        Rappeler
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Reservation;