import { useState, useEffect } from 'react';
import axios from 'axios';
import { Dialog, Listbox, Transition } from '@headlessui/react';
import AppURL from '../components/AppUrl';


interface UserData {
    name: string;
    email: string;
    id: string;
    role: string;
}

interface TimeEntry {
    id: string;
    date: string;
    startTime: string;
    endTime: string;
    breakDuration: number;
    employeeId: string;
    employeeName?: string;
}

interface Employee extends UserData { }

const TimeCard = () => {
    const [currentUser, setCurrentUser] = useState<UserData | null>(null);
    const [currentDate] = useState(new Date().toISOString().split('T')[0]);
    const [, setIsWorking] = useState(false);
    const [startTime, setStartTime] = useState('');
    const [, setEndTime] = useState('');
    const [breakDuration, setBreakDuration] = useState(30);
    const [showBreakModal, setShowBreakModal] = useState(false);
    const [timeEntries, setTimeEntries] = useState<TimeEntry[]>([]);
    const [employees, setEmployees] = useState<Employee[]>([]);
    const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null);
    const [dateRange, setDateRange] = useState({
        start: currentDate,
        end: currentDate
    });
    const [showDateModal, setShowDateModal] = useState(false);
    const [reportData, setReportData] = useState<any>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');

    const breakOptions = [15, 30, 45, 60, 75, 90];

    useEffect(() => {
        const fetchInitialData = async () => {
            try {
                const userData = localStorage.getItem('userData');
                if (!userData) {
                    throw new Error('Utilisateur non connecté');
                }

                const parsedUser = JSON.parse(userData) as UserData;
                setCurrentUser(parsedUser);

                if (parsedUser.role === 'manager') {
                    const employeesResponse = await axios.get(`${AppURL}/api/employees/All`);

                    // Normaliser les données pour s'assurer qu'on a toujours un tableau
                    let employeesData = Array.isArray(employeesResponse.data)
                        ? employeesResponse.data
                        : employeesResponse.data.employees || [];

                    // Transformer les _id en id pour une utilisation cohérente dans le frontend
                    employeesData = employeesData.map((employee: { _id: any; id: any; }) => ({
                        ...employee,
                        id: employee._id || employee.id // Utilise _id si existant, sinon garde id
                    }));

                    setEmployees(employeesData);
                }

                if (parsedUser.role === 'employe') {
                    const timeEntriesResponse = await axios.get(`${AppURL}/api/time-entries`, {
                        params: {
                            employeeId: parsedUser.id,
                            date: currentDate
                        }
                    });
                    // Extraction du tableau des entrées
                    const entries = Array.isArray(timeEntriesResponse.data)
                        ? timeEntriesResponse.data
                        : timeEntriesResponse.data.timeEntries || [];

                    setTimeEntries(entries);

                    // Déterminer si l'employé travaille actuellement
                    const activeEntry = entries.find((entry: { date: string; endTime: any; }) =>
                        entry.date === currentDate && !entry.endTime
                    );

                    // Mettre à jour isWorking en fonction de la présence d'une entrée active
                    setIsWorking(!!activeEntry);

                    // Mettre à jour l'heure de début si une entrée existe
                    if (activeEntry) {
                        setStartTime(activeEntry.startTime);
                    } else {
                        setStartTime('');
                    }
                }

            } catch (err) {
                console.error('Erreur de chargement:', err);
                setError('Erreur lors du chargement des données');
            } finally {
                setLoading(false);
            }
        };

        fetchInitialData();
    }, [currentDate]);

    const handleStartWork = async () => {
        try {
            const now = new Date();
            const timeString = now.toTimeString().substring(0, 5);

            const response = await axios.post(`${AppURL}/api/time-entries`, {
                employeeId: currentUser?.id,
                date: currentDate,
                startTime: timeString
            });

            setStartTime(timeString);
            setIsWorking(true);
            setTimeEntries([...timeEntries, response.data.timeEntry]);
        } catch (err) {
            console.error('Erreur:', err);
            setError('Erreur lors du démarrage du travail');
        }
    };

    const handleEndWork = () => {
        setShowBreakModal(true);
    };

    const confirmEndWork = async () => {
        try {
            const now = new Date();
            const timeString = now.toTimeString().substring(0, 5);

            const response = await axios.put(`${AppURL}/api/time-entries/${currentUser?.id}`, {
                endTime: timeString,
                breakDuration,
                date: currentDate
            });

            setEndTime(timeString);
            setIsWorking(false);
            setShowBreakModal(false);
            setTimeEntries(timeEntries.map(entry =>
                entry.id === response.data.timeEntry.id ? response.data.timeEntry : entry
            ));
        } catch (err) {
            console.error('Erreur:', err);
            setError('Erreur lors de la fin du travail');
        }
    };

    const generateEmployeeReport = async () => {
        if (!selectedEmployee) return;

        try {
            const response = await axios.get(`${AppURL}/api/time-entries/report`, {
                params: {
                    employeeId: selectedEmployee.id,
                    startDate: dateRange.start,
                    endDate: dateRange.end
                }
            });
            setReportData(response.data);

            setShowDateModal(false);
        } catch (err) {
            console.error('Erreur:', err);
            setError('Erreur lors de la génération du rapport');
        }
    };

    if (loading) return <div className="p-4">Chargement...</div>;
    if (error) return <div className="p-4 text-red-500">{error}</div>;
    if (!currentUser) return <div className="p-4">Utilisateur non connecté</div>;

    if (currentUser.role === 'employe') {
        // Trouver l'entrée du jour
        const todayEntry = timeEntries.find(entry => entry.date === currentDate);
        const hasEntryToday = todayEntry !== undefined;
        const isCurrentlyWorking = hasEntryToday && !todayEntry.endTime;

        return (
            <div className="bg-white rounded-lg shadow-md p-6 max-w-md mx-auto mt-20">
                <h2 className="text-xl font-bold mb-4">Fiche de temps - {currentDate}</h2>
                <p className="mb-4">Bonjour {currentUser.name}</p>

                {!hasEntryToday ? (
                    <button
                        onClick={handleStartWork}
                        className="w-full bg-green-500 hover:bg-green-600 text-white py-2 px-4 rounded-md transition"
                    >
                        Commencer le travail
                    </button>
                ) : isCurrentlyWorking ? (
                    <div className="space-y-4">
                        <div className="flex justify-between">
                            <span>Heure de début:</span>
                            <span className="font-medium">{startTime}</span>
                        </div>

                        <button
                            onClick={handleEndWork}
                            className="w-full bg-red-500 hover:bg-red-600 text-white py-2 px-4 rounded-md transition"
                        >
                            Terminer le travail
                        </button>
                    </div>
                ) : (
                    <div className="space-y-2">
                        <div className="flex justify-between">
                            <span>Heure de début:</span>
                            <span className="font-medium">{todayEntry.startTime}</span>
                        </div>
                        <div className="flex justify-between">
                            <span>Heure de fin:</span>
                            <span className="font-medium">{todayEntry.endTime}</span>
                        </div>
                        <div className="flex justify-between">
                            <span>Pause:</span>
                            <span className="font-medium">{todayEntry.breakDuration || 0} minutes</span>
                        </div>
                    </div>
                )}

                <div className="mt-6">
                    <h3 className="font-semibold mb-2">Historique aujourd'hui</h3>
                    {timeEntries
                        .filter(entry => entry.date === currentDate)
                        .map((entry, index) => {
                            // Calcul des heures travaillées
                            const start = new Date(`${entry.date}T${entry.startTime}`);
                            const end = entry.endTime ? new Date(`${entry.date}T${entry.endTime}`) : new Date();
                            const totalMinutes = (end.getTime() - start.getTime()) / (1000 * 60);
                            const workedMinutes = totalMinutes - (entry.breakDuration || 0);

                            // Formatage des heures
                            const formatTime = (minutes: number) => {
                                const hours = Math.floor(minutes / 60);
                                const mins = Math.floor(minutes % 60);
                                return `${hours}h${mins.toString().padStart(2, '0')}`;
                            };

                            return (
                                <div key={`entry-${entry.id || index}`} className="border-t pt-2 mt-2">
                                    <div className="grid grid-cols-2 gap-2">
                                        <div>
                                            <p className="font-medium">Période de travail</p>
                                            <p>{entry.startTime} - {entry.endTime || 'En cours'}</p>
                                        </div>
                                        <div>
                                            <p className="font-medium">Durée totale</p>
                                            <p>{formatTime(totalMinutes)}</p>
                                        </div>
                                    </div>
                                    <div className="grid grid-cols-2 gap-2 mt-2">
                                        <div>
                                            <p className="font-medium">Pause</p>
                                            <p>{entry.breakDuration || 0} minutes</p>
                                        </div>
                                        <div>
                                            <p className="font-medium">Heures effectives</p>
                                            <p>{formatTime(workedMinutes)}</p>
                                        </div>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div>

                <Transition show={showBreakModal}>
                    <Dialog onClose={() => setShowBreakModal(false)} className="fixed z-10 inset-0 overflow-y-auto">
                        <div className="flex items-center justify-center min-h-screen">
                            <div className="fixed inset-0 bg-black opacity-30" />

                            <div className="relative bg-white rounded-lg max-w-sm mx-auto p-6">
                                <Dialog.Title className="text-lg font-bold mb-4">Durée de la pause</Dialog.Title>

                                <div className="grid grid-cols-3 gap-2 mb-4">
                                    {breakOptions.map(minutes => (
                                        <button
                                            key={`break-${minutes}`}
                                            onClick={() => setBreakDuration(minutes)}
                                            className={`py-2 px-4 rounded ${breakDuration === minutes ? 'bg-blue-500 text-white' : 'bg-gray-200'}`}
                                        >
                                            {minutes} min
                                        </button>
                                    ))}
                                </div>

                                <div className="flex justify-end space-x-2">
                                    <button
                                        onClick={() => setShowBreakModal(false)}
                                        className="px-4 py-2 border rounded"
                                    >
                                        Annuler
                                    </button>
                                    <button
                                        onClick={confirmEndWork}
                                        className="px-4 py-2 bg-blue-500 text-white rounded"
                                    >
                                        Confirmer
                                    </button>
                                </div>
                            </div>
                        </div>
                    </Dialog>
                </Transition>
            </div>
        );
    }

    // Vue Manager - Design Responsive
    return (
        <div className="bg-white rounded-lg shadow-md p-4 md:p-6 mx-2 md:mx-auto mt-20 md:mt-20 ">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-6 gap-4">
                <div>
                    <h2 className="text-lg md:text-xl font-bold">Gestion du temps des employés</h2>
                    <p className="text-sm md:text-base text-gray-600">Manager: {currentUser.name}</p>
                </div>

                <div className="w-full md:w-auto">
                    <label className="block text-sm font-medium mb-1">Sélectionner un employé:</label>
                    <Listbox value={selectedEmployee} onChange={setSelectedEmployee}>
                        <div className="relative">
                            <Listbox.Button className="w-full bg-white border rounded-md py-2 px-3 text-sm md:text-base text-left">
                                {selectedEmployee?.name || "Sélectionner..."}
                            </Listbox.Button>
                            <Listbox.Options className="absolute z-10 w-full bg-white border rounded-md shadow-lg mt-1 max-h-60 overflow-auto text-sm md:text-base">
                                {employees.length > 0 ? (
                                    employees.map(employee => (
                                        <Listbox.Option
                                            key={employee.id}
                                            value={employee}
                                            className={({ active }) => `px-3 py-1 ${active ? 'bg-blue-100' : ''}`}
                                        >
                                            {employee.name} ({employee.role})
                                        </Listbox.Option>
                                    ))
                                ) : (
                                    <div className="px-3 py-1 text-gray-500 text-sm">Aucun employé disponible</div>
                                )}
                            </Listbox.Options>
                        </div>
                    </Listbox>
                </div>
            </div>

            {selectedEmployee && (
                <div className="space-y-4">
                    <button
                        onClick={() => setShowDateModal(true)}
                        className="w-full md:w-auto bg-green-950 hover:bg-green-600 text-white py-2 px-4 rounded text-sm md:text-base"
                    >
                        Sélectionner une période
                    </button>

                    {reportData?.report && (
                        <div className="mt-4 border-t pt-4">
                            <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-3">
                                <h3 className="font-bold text-base md:text-lg">
                                    Rapport pour {selectedEmployee.name}
                                </h3>
                                <p className="text-sm text-gray-600">
                                    {reportData.report.dateRange.start} à {reportData.report.dateRange.end}
                                </p>
                            </div>

                            {/* Version mobile - Cards */}
                            <div className="md:hidden space-y-3">
                                {reportData.report.dailyData.map((day: any) => (
                                    <div key={day.date} className="border rounded-lg p-3 shadow-sm">
                                        <div className="font-medium">{day.date}</div>
                                        <div className="grid grid-cols-2 gap-2 mt-2 text-sm">
                                            <div>Début: <span className="font-medium">{day.startTime}</span></div>
                                            <div>Fin: <span className="font-medium">{day.endTime}</span></div>
                                            <div>Heures travaillées: <span className="font-medium">{day.hoursWorked}h</span></div>
                                            <div>Pause: <span className="font-medium">{day.breakDuration}min</span></div>
                                        </div>
                                    </div>
                                ))}
                            </div>

                            {/* Version desktop - Table */}
                            <div className="hidden md:block overflow-x-auto">
                                <table className="min-w-full divide-y divide-gray-200">
                                    <thead className="bg-gray-50">
                                        <tr>
                                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Date</th>
                                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Début</th>
                                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Fin</th>
                                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Heures travaillées</th>
                                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase">Pause</th>
                                        </tr>
                                    </thead>
                                    <tbody className="bg-white divide-y divide-gray-200">
                                        {reportData.report.dailyData.map((day: any) => (
                                            <tr key={day.date}>
                                                <td className="px-4 py-2 whitespace-nowrap text-sm">{day.date}</td>
                                                <td className="px-4 py-2 whitespace-nowrap text-sm">{day.startTime}</td>
                                                <td className="px-4 py-2 whitespace-nowrap text-sm">{day.endTime}</td>
                                                <td className="px-4 py-2 whitespace-nowrap text-sm">{day.hoursWorked}h</td>
                                                <td className="px-4 py-2 whitespace-nowrap text-sm">{day.breakDuration}min</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-3 gap-3 mt-4 p-3 bg-gray-50 rounded-lg">
                                <div className="flex flex-col items-center p-2 bg-white rounded shadow-sm">
                                    <span className="text-sm font-medium text-gray-600">Total heures</span>
                                    <span className="text-xl font-bold">{reportData.report.totalHours} h</span>
                                </div>
                                <div className="flex flex-col items-center p-2 bg-white rounded shadow-sm">
                                    <span className="text-sm font-medium text-gray-600">Total pauses</span>
                                    <span className="text-xl font-bold">{reportData.report.totalBreak} min</span>
                                    <span className="text-xs text-gray-500">({(reportData.report.totalBreak / 60).toFixed(2)} h)</span>
                                </div>
                                <div className="flex flex-col items-center p-2 bg-white rounded shadow-sm">
                                    <span className="text-sm font-medium text-gray-600">Heures payées</span>
                                    <span className="text-xl font-bold">
                                        {(reportData.report.totalHours - (reportData.report.totalBreak / 60)).toFixed(2)} h
                                    </span>
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            )}

            {/* Modal (inchangé mais responsive) */}
            <Transition show={showDateModal}>
                <Dialog onClose={() => setShowDateModal(false)} className="fixed z-10 inset-0 overflow-y-auto">
                    <div className="flex items-center justify-center min-h-screen px-4">
                        <div className="fixed inset-0 bg-black opacity-30" />
                        <div className="relative bg-white rounded-lg w-full max-w-sm mx-auto p-4 md:p-6">
                            <Dialog.Title className="text-lg font-bold mb-4">Sélectionner une période</Dialog.Title>
                            <div className="space-y-3">
                                <div>
                                    <label className="block mb-1 text-sm">Date de début:</label>
                                    <input
                                        type="date"
                                        value={dateRange.start}
                                        onChange={(e) => setDateRange({ ...dateRange, start: e.target.value })}
                                        className="w-full p-2 border rounded text-sm"
                                    />
                                </div>
                                <div>
                                    <label className="block mb-1 text-sm">Date de fin:</label>
                                    <input
                                        type="date"
                                        value={dateRange.end}
                                        onChange={(e) => setDateRange({ ...dateRange, end: e.target.value })}
                                        className="w-full p-2 border rounded text-sm"
                                    />
                                </div>
                            </div>
                            <div className="flex justify-end space-x-2 mt-4">
                                <button
                                    onClick={() => setShowDateModal(false)}
                                    className="px-3 py-1 md:px-4 md:py-2 border rounded text-sm"
                                >
                                    Annuler
                                </button>
                                <button
                                    onClick={generateEmployeeReport}
                                    className="px-3 py-1 md:px-4 md:py-2 bg-blue-500 text-white rounded text-sm"
                                >
                                    Générer
                                </button>
                            </div>
                        </div>
                    </div>
                </Dialog>
            </Transition>
        </div>
    );

};

export default TimeCard;