import { Component, ChangeEvent, FormEvent } from 'react';
import axios from 'axios';
import AppURL from '../components/AppUrl';

interface Facture {
    _id: string;
    name: string;
    type: string;
    total: string;
    note: string;
    filePath: string;
    createdAt: string;
}


interface State {
    factures: Facture[];
    showModal: boolean;
    showViewModal: boolean;
    showDeleteDialog: boolean;
    currentFacture: Facture | null;
    newFactureName: string;
    newFactureType: string;
    newFactureTotal: string;
    newFactureNote: string;
    file: File | null;
    imagePreview: string | null;
    loading: boolean;
    isEditing: boolean;
    selectedDate: string | null;
    newFactureDate: string; // Ajoutez cette ligne


}


class FactureManager extends Component<{}, State> {

    state: State = {
        factures: [],
        showModal: false,
        showViewModal: false,
        showDeleteDialog: false,
        currentFacture: null,
        newFactureName: '',
        newFactureType: 'achat',
        newFactureTotal: '',
        newFactureNote: '',
        file: null,
        imagePreview: null,
        loading: true,
        isEditing: false,
        selectedDate: null,
        newFactureDate: ''

    };

    handleDateChange = (e: ChangeEvent<HTMLInputElement>) => {
        this.setState({ selectedDate: e.target.value }, () => {
            this.fetchFactures(); // Appel après la mise à jour de l'état
        });
    };

    componentDidMount() {
        this.fetchFactures();
    }

    fetchFactures = async () => {
        this.setState({ loading: true });
        try {
            const response = await axios.get<Facture[]>(`${AppURL}/api/factures/getAll`);
            let filteredFactures = response.data;

            if (this.state.selectedDate) {
                const selectedDateStr = new Date(this.state.selectedDate).toISOString().split('T')[0];
                filteredFactures = response.data.filter(facture => {
                    const factureDateStr = new Date(facture.createdAt).toISOString().split('T')[0];
                    return factureDateStr === selectedDateStr;
                });
            }

            this.setState({
                factures: filteredFactures,
                loading: false
            });
        } catch (error) {
            console.error("Error fetching factures:", error);
            this.setState({ loading: false });
        }
    };



    toggleModal = () => {
        this.setState(prevState => ({
            showModal: !prevState.showModal,
            isEditing: false,
            currentFacture: null,
            newFactureName: '',
            newFactureType: 'achat',
            newFactureTotal: '',
            newFactureNote: '',
            file: null,
            imagePreview: null
        }));
    };

    toggleViewModal = (facture: Facture | null = null) => {
        this.setState(prevState => ({
            showViewModal: !prevState.showViewModal,
            currentFacture: facture,
            imagePreview: facture ? `${AppURL}/${facture.filePath}` : null
        }));
    };

    toggleDeleteDialog = (facture: Facture | null = null) => {
        this.setState(prevState => ({
            showDeleteDialog: !prevState.showDeleteDialog,
            currentFacture: facture
        }));
    };

    openEditModal = (facture: Facture) => {
        const factureDate = new Date(facture.createdAt).toISOString().split('T')[0];
        this.setState({
            currentFacture: facture,
            newFactureName: facture.name,
            newFactureType: facture.type,
            newFactureTotal: facture.total,
            newFactureNote: facture.note || '',
            newFactureDate: factureDate, // Initialisez avec la date de la facture
            imagePreview: `${AppURL}/${facture.filePath}`,
            isEditing: true,
            showModal: true,
            showViewModal: false
        });
    };

    handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        const selectedFile = e.target.files?.[0];
        if (selectedFile) {
            this.setState({ file: selectedFile });

            const reader = new FileReader();
            reader.onloadend = () => {
                this.setState({ imagePreview: reader.result as string });
            };
            reader.readAsDataURL(selectedFile);
        }
    };

    handleInputChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        } as unknown as Pick<State, keyof State>);
    };

    handleSubmit = async (e: FormEvent) => {
        e.preventDefault();

        const { currentFacture, newFactureName, newFactureType, newFactureTotal, newFactureDate, newFactureNote, file, isEditing } = this.state;

        if (!file && !currentFacture && !isEditing) return;

        const data = new FormData();
        data.append('name', newFactureName);
        data.append('type', newFactureType);
        data.append('total', newFactureTotal);
        data.append('note', newFactureNote);
        data.append('createdAt', new Date(newFactureDate).toISOString());
        if (file) {
            data.append('file', file);
        }
        try {
            if (isEditing && currentFacture) {
                await axios.put(`${AppURL}/api/factures/${currentFacture._id}`, data, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });
            } else {
                await axios.post(`${AppURL}/api/factures/create`, data, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });
            }

            this.fetchFactures();
            this.toggleModal();
        } catch (error) {
            console.error("Error saving facture:", error);
        }
    };

    handleDelete = async () => {
        const { currentFacture } = this.state;
        if (!currentFacture) return;

        try {
            await axios.delete(`${AppURL}/api/factures/${currentFacture._id}`);
            this.fetchFactures();
            this.toggleDeleteDialog();
            this.toggleViewModal();
        } catch (error) {
            console.error("Error deleting facture:", error);
        }
    };

    getTypeColor = (type: string) => {
        switch (type) {
            case 'achat':
                return 'bg-red-100 border-red-200 hover:bg-red-50';
            case 'vente':
                return 'bg-green-100 border-green-200 hover:bg-green-50';
            default:
                return 'bg-gray-100 border-gray-200 hover:bg-gray-50';
        }
    };

    getTypeTextColor = (type: string) => {
        switch (type) {
            case 'achat':
                return 'text-red-800';
            case 'vente':
                return 'text-green-800';
            default:
                return 'text-gray-800';
        }
    };

    render() {
        const {
            factures,
            loading,
            showModal,
            showViewModal,
            showDeleteDialog,
            currentFacture,
            newFactureName,
            newFactureType,
            newFactureTotal,
            newFactureNote,
            imagePreview,
            isEditing
        } = this.state;

        if (loading) {
            return (
                <div className="flex justify-center items-center h-screen">
                    <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
                </div>
            );
        }

        return (
            <div className="container mx-auto px-4 py-8 max-w-full mt-10">
                {/* En-tête */}
                <div className="flex justify-between items-center mb-8">
                    <div>
                        <h1 className="text-2xl font-bold text-gray-800">Gestion des Factures</h1>
                        <p className="text-gray-500">Liste de toutes vos factures</p>
                    </div>
                    <button
                        onClick={this.toggleModal}
                        className="bg-green-950 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg flex items-center space-x-2 transition-colors"
                    >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                        </svg>
                        <span>Nouvelle facture</span>
                    </button>
                </div>


                <div className="mb-6 flex items-center space-x-4">
                    <div>
                        <label htmlFor="dateFilter" className="block text-sm font-medium text-gray-700 mb-1">
                            Filtrer par date
                        </label>
                        <input
                            type="date"
                            id="dateFilter"
                            value={this.state.selectedDate || ''}
                            onChange={this.handleDateChange}
                            className="px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-green-500 focus:border-green-500"
                        />
                    </div>
                    {this.state.selectedDate && (
                        <button
                            onClick={() => this.setState({ selectedDate: null }, this.fetchFactures)}
                            className="mt-6 px-3 py-1.5 text-sm text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
                        >
                            Effacer le filtre
                        </button>
                    )}
                </div>







                {/* Liste des factures */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {factures.length === 0 ? (
                        <div className="col-span-full text-center py-12">
                            <svg xmlns="http://www.w3.org/2000/svg" className="h-12 w-12 mx-auto text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M9 17v-2m3 2v-4m3 4v-6m2 10H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                            </svg>
                            <h3 className="mt-2 text-lg font-medium text-gray-900">
                                {this.state.selectedDate ? "Aucune facture pour cette date" : "Aucune facture"}
                            </h3>
                            <p className="mt-1 text-gray-500">
                                {this.state.selectedDate ? "Essayez une autre date ou ajoutez une nouvelle facture" : "Commencez par ajouter votre première facture"}
                            </p>
                            <div className="mt-6">
                                <button
                                    onClick={this.toggleModal}
                                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-green-950 hover:bg-blue-700 focus:outline-none"
                                >
                                    Ajouter une facture
                                </button>
                                {this.state.selectedDate && (
                                    <button
                                        onClick={() => this.setState({ selectedDate: null }, this.fetchFactures)}
                                        className="ml-3 inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none"
                                    >
                                        Afficher toutes les factures
                                    </button>
                                )}
                            </div>
                        </div>
                    ) : (
                        factures.map(facture => (
                            <div
                                key={facture._id}
                                className={`border rounded-lg p-4 cursor-pointer transition-all ${this.getTypeColor(facture.type)}`}
                                onClick={() => this.toggleViewModal(facture)}
                            >
                                <div className="flex justify-between items-start">
                                    <div>
                                        <h3 className={`font-medium ${this.getTypeTextColor(facture.type)}`}>{facture.name}</h3>
                                        <p className="text-sm text-gray-500 mt-1">
                                            {new Date(facture.createdAt).toLocaleDateString()}
                                        </p>
                                    </div>
                                    <span className={`px-2 py-1 text-xs font-semibold rounded-full ${this.getTypeTextColor(facture.type)} ${this.getTypeColor(facture.type)}`}>
                                        {facture.type}
                                    </span>
                                </div>
                                <div className="mt-2">
                                    <p className="text-lg font-semibold">
                                        {parseFloat(facture.total).toFixed(2)} $
                                    </p>
                                </div>
                            </div>
                        ))
                    )}
                </div>

                {/* Modal de création */}
                {showModal && !isEditing && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                            <div className="p-6">
                                <div className="flex justify-between items-center mb-4">
                                    <h3 className="text-xl font-semibold text-gray-900">Nouvelle facture</h3>
                                    <button
                                        onClick={this.toggleModal}
                                        className="text-gray-400 hover:text-gray-500"
                                    >
                                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                        </svg>
                                    </button>
                                </div>

                                <form onSubmit={this.handleSubmit} className="space-y-4">
                                    <div>
                                        <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">Nom de la facture</label>
                                        <input
                                            type="text"
                                            id="name"
                                            name="newFactureName"
                                            value={newFactureName}
                                            onChange={this.handleInputChange}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                            required
                                        />
                                    </div>

                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label htmlFor="type" className="block text-sm font-medium text-gray-700 mb-1">Type</label>
                                            <select
                                                id="type"
                                                name="newFactureType"
                                                value={newFactureType}
                                                onChange={this.handleInputChange}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                                required
                                            >
                                                <option value="achat">Achat</option>
                                                <option value="vente">Vente</option>
                                                <option value="autre">Autre</option>
                                            </select>
                                        </div>
                                        <div>
                                            <label htmlFor="total" className="block text-sm font-medium text-gray-700 mb-1">Montant ($)</label>
                                            <input
                                                type="number"
                                                id="total"
                                                name="newFactureTotal"
                                                value={newFactureTotal}
                                                onChange={this.handleInputChange}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                                required
                                                step="0.01"
                                                min="0"
                                            />
                                        </div>
                                    </div>
                                    {/* Ajoutez ce bloc dans les deux formulaires (création et modification) */}
                                    <div>
                                        <label htmlFor="date" className="block text-sm font-medium text-gray-700 mb-1">Date</label>
                                        <input
                                            type="date"
                                            id="date"
                                            name="newFactureDate"
                                            value={this.state.newFactureDate}
                                            onChange={this.handleInputChange}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                            required
                                        />
                                    </div>

                                    <div>
                                        <label htmlFor="note" className="block text-sm font-medium text-gray-700 mb-1">Notes</label>
                                        <textarea
                                            id="note"
                                            name="newFactureNote"
                                            rows={3}
                                            value={newFactureNote}
                                            onChange={this.handleInputChange}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Fichier</label>
                                        <div className="mt-1 flex items-center">
                                            <label className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 cursor-pointer">
                                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                                                </svg>
                                                Choisir un fichier
                                                <input
                                                    type="file"
                                                    className="hidden"
                                                    onChange={this.handleFileChange}
                                                    accept="image/*"
                                                    required
                                                />
                                            </label>
                                            <span className="ml-2 text-sm text-gray-500">
                                                {this.state.file ? this.state.file.name : 'Aucun fichier sélectionné'}
                                            </span>
                                        </div>
                                        {imagePreview && (
                                            <div className="mt-2">
                                                <img
                                                    src={imagePreview}
                                                    alt="Aperçu"
                                                    className="max-w-full max-h-48 object-contain border border-gray-200 rounded-md"
                                                />
                                            </div>
                                        )}
                                    </div>

                                    <div className="flex justify-end space-x-3 pt-4">
                                        <button
                                            type="button"
                                            onClick={this.toggleModal}
                                            className="px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                                        >
                                            Annuler
                                        </button>
                                        <button
                                            type="submit"
                                            className="px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-950 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                                        >
                                            Enregistrer
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                )}

                {/* Modal de modification */}
                {showModal && isEditing && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                            <div className="p-6">
                                <div className="flex justify-between items-center mb-4">
                                    <h3 className="text-xl font-semibold text-gray-900">Modifier la facture</h3>
                                    <button
                                        onClick={this.toggleModal}
                                        className="text-gray-400 hover:text-gray-500"
                                    >
                                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                        </svg>
                                    </button>
                                </div>

                                <form onSubmit={this.handleSubmit} className="space-y-4">
                                    <div>
                                        <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">Nom de la facture</label>
                                        <input
                                            type="text"
                                            id="name"
                                            name="newFactureName"
                                            value={newFactureName}
                                            onChange={this.handleInputChange}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                            required
                                        />
                                    </div>

                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label htmlFor="type" className="block text-sm font-medium text-gray-700 mb-1">Type</label>
                                            <select
                                                id="type"
                                                name="newFactureType"
                                                value={newFactureType}
                                                onChange={this.handleInputChange}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                                required
                                            >
                                                <option value="achat">Achat</option>
                                                <option value="vente">Vente</option>
                                                <option value="autre">Autre</option>
                                            </select>
                                        </div>
                                        <div>
                                            <label htmlFor="total" className="block text-sm font-medium text-gray-700 mb-1">Montant ($)</label>
                                            <input
                                                type="number"
                                                id="total"
                                                name="newFactureTotal"
                                                value={newFactureTotal}
                                                onChange={this.handleInputChange}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                                required
                                                step="0.01"
                                                min="0"
                                            />
                                        </div>
                                    </div>

                                    <div>
                                        <label htmlFor="note" className="block text-sm font-medium text-gray-700 mb-1">Notes</label>
                                        <textarea
                                            id="note"
                                            name="newFactureNote"
                                            rows={3}
                                            value={newFactureNote}
                                            onChange={this.handleInputChange}
                                            className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-1">Fichier</label>
                                        <div className="mt-1 flex items-center">
                                            <label className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 cursor-pointer">
                                                <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                                                </svg>
                                                Changer le fichier
                                                <input
                                                    type="file"
                                                    className="hidden"
                                                    onChange={this.handleFileChange}
                                                    accept="image/*"
                                                />
                                            </label>
                                            <span className="ml-2 text-sm text-gray-500">
                                                {this.state.file ? this.state.file.name : 'Fichier existant conservé'}
                                            </span>
                                        </div>
                                        {imagePreview && (
                                            <div className="mt-2">
                                                <img
                                                    src={imagePreview}
                                                    alt="Aperçu"
                                                    className="max-w-full max-h-48 object-contain border border-gray-200 rounded-md"
                                                />
                                            </div>
                                        )}
                                    </div>

                                    <div className="flex justify-end space-x-3 pt-4">
                                        <button
                                            type="button"
                                            onClick={this.toggleModal}
                                            className="px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                                        >
                                            Annuler
                                        </button>
                                        <button
                                            type="submit"
                                            className="px-4 py-2 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-green-950 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                                        >
                                            Mettre à jour
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                )}

                {/* Modal de détails */}
                {showViewModal && currentFacture && (
                    <div className="fixed inset-0 bg-green-950 bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
                            <div className="p-6">
                                <div className="flex justify-between items-start mb-4">
                                    <div>
                                        <h3 className="text-xl font-semibold text-gray-900">{currentFacture.name}</h3>
                                        <p className="text-sm text-gray-500">
                                            Créée le {new Date(currentFacture.createdAt).toLocaleDateString()}
                                        </p>
                                    </div>
                                    <div className="flex space-x-2">
                                        <button
                                            onClick={() => this.openEditModal(currentFacture)}
                                            className="p-2 text-gray-500 hover:text-blue-600 hover:bg-blue-50 rounded-full transition-colors"
                                            title="Modifier"
                                        >
                                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                                <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                                            </svg>
                                        </button>
                                        <button
                                            onClick={() => this.toggleDeleteDialog(currentFacture)}
                                            className="p-2 text-gray-500 hover:text-red-600 hover:bg-red-50 rounded-full transition-colors"
                                            title="Supprimer"
                                        >
                                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                                <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                                            </svg>
                                        </button>
                                        <button
                                            onClick={() => this.toggleViewModal()}
                                            className="p-2 text-gray-500 hover:text-gray-600 hover:bg-gray-100 rounded-full transition-colors"
                                            title="Fermer"
                                        >
                                            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                            </svg>
                                        </button>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-6 mb-6">
                                    <div className="bg-gray-50 p-4 rounded-lg">
                                        <p className="text-sm font-medium text-gray-500 mb-1">Type</p>
                                        <p className="text-lg font-medium">
                                            <span className={`px-2 py-1 rounded-full text-xs font-semibold 
                        ${currentFacture.type === 'achat' ? 'bg-red-100 text-red-800' :
                                                    currentFacture.type === 'vente' ? 'bg-green-100 text-green-800' :
                                                        'bg-gray-100 text-gray-800'}`}>
                                                {currentFacture.type}
                                            </span>
                                        </p>
                                    </div>
                                    <div className="bg-gray-50 p-4 rounded-lg">
                                        <p className="text-sm font-medium text-gray-500 mb-1">Montant</p>
                                        <p className="text-lg font-medium">{parseFloat(currentFacture.total)} $</p>
                                    </div>
                                </div>

                                {currentFacture.note && (
                                    <div className="mb-6">
                                        <p className="text-sm font-medium text-gray-500 mb-2">Notes</p>
                                        <div className="bg-gray-50 p-4 rounded-lg">
                                            <p className="text-gray-700 whitespace-pre-line">{currentFacture.note}</p>
                                        </div>
                                    </div>
                                )}

                                <div>
                                    <p className="text-sm font-medium text-gray-500 mb-2">Document</p>
                                    <div className="border-2 border-dashed border-gray-200 rounded-lg p-4 flex flex-col items-center justify-center">
                                        <img
                                            src={`${AppURL}/${currentFacture.filePath}`}
                                            alt="Facture"
                                            className="max-w-full max-h-96 object-contain"
                                        />
                                        <a
                                            href={`${AppURL}/${currentFacture.filePath}`}
                                            download
                                            className="mt-4 inline-flex items-center px-3 py-1.5 border border-gray-300 shadow-sm text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                                        >
                                            <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 3H5a2 2 0 00-2 2v3m0 8v3a2 2 0 002 2h3m8-18h3a2 2 0 012 2v3m0 8v3a2 2 0 01-2 2h-3" />
                                            </svg>

                                            plein écran
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Dialogue de confirmation de suppression */}
                {showDeleteDialog && (
                    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                        <div className="bg-white rounded-lg shadow-xl w-full max-w-md">
                            <div className="p-6">
                                <div className="flex items-center justify-center w-12 h-12 mx-auto bg-red-100 rounded-full">
                                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                                    </svg>
                                </div>
                                <div className="mt-3 text-center sm:mt-5">
                                    <h3 className="text-lg leading-6 font-medium text-gray-900">
                                        Supprimer la facture
                                    </h3>
                                    <div className="mt-2">
                                        <p className="text-sm text-gray-500">
                                            Êtes-vous sûr de vouloir supprimer la facture "{currentFacture?.name}" ? Cette action est irréversible.
                                        </p>
                                    </div>
                                </div>
                                <div className="mt-5 sm:mt-6 sm:grid sm:grid-cols-2 sm:gap-3 sm:grid-flow-row-dense">
                                    <button
                                        type="button"
                                        onClick={() => this.toggleDeleteDialog()}
                                        className="w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 sm:col-start-2 sm:text-sm"
                                    >
                                        Annuler
                                    </button>
                                    <button
                                        type="button"
                                        onClick={this.handleDelete}
                                        className="mt-3 w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:mt-0 sm:col-start-1 sm:text-sm"
                                    >
                                        Supprimer
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        );
    }
}

export default FactureManager;